    let time = (Date.now() - localStorage.getItem('nowTime')) / 1000
    if (time <= timeNum) {
      this.smscountdown.timer = Math.ceil(timeNum - time)
      this.smscountdown.enable = localStorage.getItem('PointEnable')
      this.interval()
    }
