        url = '/customer/getCompanyUserInfo/1?member=1&employee=1&nameOrPhone=' + this.nameOrPhone
      } else {
        url = '/customer/getCompanyUserInfo/1?nameOrPhone=' + this.nameOrPhone
      }
      this.http.get(this.$store.state.prefix + url).then(res => {
