<template>
<div class="weixinpay">
   <Steps :current="currentStep">
        <Step title="é€‰æ‹©æ—¶é•¿" content=""></Step>
        <Step title="æ‰«ç ä»˜æ¬¾" content=""></Step>
        <Step title="ç»­è´¹å®Œæˆ" content=""></Step>
    </Steps>
    <div class="stepContent">
      <div class="chooseMoney" v-if="currentStep==0">
        <ul>
          <li v-for="ri in RenewInfo" v-text="ri.month+'æœˆ'" :class="{active:ri.active}" @click="selectMoney(ri)"></li>
        </ul>
        <div v-if="RenewInfo.length==0">ç®¡ç†å‘˜æš‚æ—¶æœªé…ç½®ç»­è´¹é‡‘é¢ï¼Œè¯·è”ç³»ç®¡ç†å‘˜è¿›è¡Œè®¾ç½®</div>
        <div style="width:80%;display:flex;font-size:1.1em;color:red" v-if="RenewInfo.length>0">
          <div style="flex:1;text-align:center">æ€»é‡‘é¢ï¼š{{realMoney}}å…ƒ</div>
          <div style="flex:1;text-align:center">åˆ°æœŸæ—¥æœŸï¼š{{util.getFormatDate(new Date(company.expireDate).getTime()+realMonth*30*24*3600*1000)}}</div>
        </div>
      </div>
      <div class="payQrCode text-center"  v-if="currentStep==1">
        <img :src="'http://qr.liantu.com/api.php?w=200&m=5&text='+code_url" alt="" v-if="code_url">
        <Row>
        <span v-text="'ä»˜è´¹é‡‘é¢:'+realMoney+'å…ƒ'" style="font-size:1.5em"></span></br>
         <Icon type="ios-information-outline"></Icon>è¯·ä½¿ç”¨å¾®ä¿¡æ‰«ä¸€æ‰«è¿›è¡Œä»˜æ¬¾</Row>
      </div>
      <div class="payStatus" v-if="currentStep==2">
          <Icon type="ios-checkmark-outline" size="100" color="#44b549"></Icon>
          <br>
          ç»­æœŸæˆåŠŸ
      </div>
    </div>
    <div class="stepOperate text-right">
        <Button type="ghost"  size="small" v-if="currentStep>0&&currentStep<2" @click="switchStep(false)">ä¸Šä¸€æ­¥</Button>
        <Button type="info"  size="small" v-if="currentStep<=2&&(!stepdone)" @click="switchStep(true)">
       ä¸‹ä¸€æ­¥</Button>
       <Button type="info"  size="small" v-if="currentStep<=2&&(stepdone)" @click="chargeOk()">
       å®Œæˆ</Button>
       

    </div>
</div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'Renew',
  props: ['wrapper', 'company'],
  data () {
    return {
      RenewInfo:[],
      currentStep: 0,
      stepdone: false,
      realMoney: 800,
      realMonth:1,
      businessId:1,
      code_url:'',
      repeateNum:0
    }
  },
  created () {
    this.getRenewinfo()
  },
  mounted () {
  },
  methods: {
    getRenewinfo(){
      this.http.post(this.$store.state.prefix+'/pubInfo/getSystemRenewInfo',{renewType:1,chargeType:1}).then(res=>{
        if(res.error==false){
          res.result.forEach((item)=>{
            item.active=false
          })
          res.result[0].active=true
          this.realMoney=res.result[0].amount
          this.realMonth=res.result[0].month
          this.businessId=res.result[0].id
          this.RenewInfo=res.result
        }
      })
    },
    selectMoney (pm) {
      for (var i in this.RenewInfo) {
        this.RenewInfo[i].active = false
      }
      this.realMoney=pm.amount
      this.realMonth=pm.month
      this.businessId=pm.id
      pm.active = true
    },
    switchStep (type) {
      if (type === true) {
        if (this.currentStep === 0) {
          var realMoney = 0
          
          var param = {
            payType: 3,
            payAmount: Number(this.realMoney),
            companyId :this.company.id,
            businessId:this.businessId,
            goodsId: 0,
            remarks:'è´¦æˆ·ç»­æœŸ'+this.realMonth+'ä¸ªæœˆ',
            companyId:this.util.getCookie("companyId")
          }
          this.http.post(this.$store.state.prefix + '/pay' , param).then (res => {
            if (res.error === false) {
              this.code_url = res.result.code_url
              this.currentStep ++
              var _this=this
              setTimeout(function(){
                  _this.checkStatus(res.result.out_trade_no)
              },3000)
              
            } else {
              this.$Message.error(res.msg)
            }
          })

        }
        if(this.currentStep === 2){
         this.stepdone = true
        }
      } else {
        this.stepdone = false
        this.currentStep -= 1
      }
    },
    chargeOk() {
       var _this=this
          for(var i=0;i<100;i++){
            if(_this.$parent[this.wrapper] === undefined){
              _this = _this.$parent
              
            }else{
              _this.$parent[this.wrapper] = false
              this.stepdone = false
              this.currentStep = 0
              _this.$parent.getCompanyinfo()
              break
            }
          }
    },
    checkStatus (out_trade_no) {
        this.http.post(this.$store.state.prefix + '/pay/getByOutTradeNo', {outTradeNo: out_trade_no}).then(res => {
        if (res.error === false) {
            if(res.result.payStatus === 1){
              this.currentStep ++
              this.switchStep(true)
            }else{
              var _this=this
              if(this.repeateNum<200){
                setTimeout(function(){
                  _this.checkStatus(out_trade_no)
                },1000)
              }
              
            }
        }
      })
    }
  }
}
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
.stepContent
  padding:20px 5px
  .chooseMoney
    ul
      width:80%
      margin:0px auto
      li
        padding: 5px 10px
        border:1px solid #ccc
        border-radius:3px
        text-align:center
        display:inline-block
        margin:0px 5px 5px 5px
        cursor:pointer
        min-width:60px
      li.active
        background:#63c168
        color:#fff
      li.coustom
        padding:0px
        width:100px
        border:0px
        input
          width:100%
          height:30px
          border:1px solid #ccc
          border-radius:3px
          padding:0px 3px
  .payQrCode
    text-align:center
    img
      margin:0px auto
  .payStatus
    text-align:center

</style>



