<template>
<div class="employeeadd">
	<div class="content-title">
      <div class="titlename">
      	<span>æ·»åŠ å‘˜å·¥</span>
      </div>
      <div class="titellink">
      	<router-link to="/employee" class="innerbtnlink">å‘˜å·¥åˆ—è¡¨</router-link>
      </div>
 	</div>
 	<div class="content">
    <Form ref="formValidate" :model="employs" class="goodseditform"  :label-width="100">
      <Form-item label="å§“å" prop="employeeName">
        <Input v-model="employs.employeeName" placeholder="è¯·è¾“å…¥"></Input>
      </Form-item>
      <Form-item label="å¤´åƒ" class="text-left">
        <uploader :config="uploaderconfig"> </uploader>
        <input type="hidden" v-model="employs.headImg">
      </Form-item>
      <Form-item label="å›¾ç‰‡é¢„è§ˆ">
        <a :href="murl + employs.headImg" target="_blank" v-if="employs.headImg">
          <img :src="murl+employs.headImg" alt="" class="goodsImgThumb">
        </a>
      </Form-item>
      <Form-item label="ç”µè¯" prop="phone">
        <Input type="text" v-model="employs.phone" number></Input>
      </Form-item>
      <Form-item label="å®¢æˆ·æ•°" prop="customerNum">
        <Input type="text" v-model="employs.customerNum" number></Input>
      </Form-item>
      <Form-item label="æŽ’åº">
        <Input type="text" v-model="employs.sort" number></Input>
      </Form-item>
      <Form-item>
        <Button type="primary" @click="handleSubmit('formValidate')">æäº¤</Button>
        <Button type="ghost" style="margin-left: 8px" @click="handleReset('formValidate')">å–æ¶ˆ</Button>
      </Form-item>
    </Form>
  </div>
</div>
</template>

<script  type="text/ecmascript-6">
import uploader from '../Util/Uploader'
export default {
  name: 'EmployeeAdd',
  data () {
    return {
      employs: {
        goodsName: '',
        goodsImg: '',
        goodsPrice: '',
        maxPoints: '',
        sort: 0,
        storageNum: '',
        goodsStatus: 1,
        goodsDesc: '',
        goodsDate: (new Date().getTime())
      },
      uploaderconfig: {
        maxSize: 5120,
        format: ['jpg', 'png', 'jpeg'],
        showUploadList: false,
        parent: 'employs',
        child: 'goodsImg'
      }
    }
  },
  components: { uploader },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          if (this.employs.id === '') {
            delete this.employs.id
          }
          this.http.post(this.$store.state.prefix + '/goods', this.employs).then(res => {
            if (res.error === false) {
              this.$Message.success('ä¿å­˜æˆåŠŸ')
              this.router.push('/goods')
            }
          })
        } else {
          this.$Message.error('éªŒè¯å¤±è´¥')
        }
      })
    }
  }
}
</script>

