<script type="text/ecmascript-6">
import ueditor from '../Ueditor'
import uploader from '../Util/Uploader'
export default {
  components: { ueditor, uploader },
  name: 'ActivityAdd',
  mounted () {
    this.over = true;
  },
  data () {
    return {
      musicList: [],
      isTeam:true,
      defaultMSg: '',
      Ueditorconfig: {
        initialFrameWidth: null,
        initialFrameHeight: 320,
        info: {},
        imageUrl: '//up.qbox.me/',
        imageActionName: 'uploadimage',
        imageFieldName: 'file',
        imageAllowFiles: ['.jpg', '.png', '.jpeg'],
        imageCompressEnable: true,
        imageCompressBorder: 1600,
        imageInsertAlign: 'none',
        imagePathFormat: 'upload/image/{yyyy}{mm}{dd}/{time}{rand:6}',
        videoUrl: '//up.qbox.me/',
        videoActionName: 'uploadvideo',
        videoFieldName: 'file',
        videoAllowFiles: ['.mp4', '.ogv', '.webm'],
        videoCompressEnable: true,
        videoCompressBorder: 1600,
        videoInsertAlign: 'none',
        videoPathFormat: 'upload/video/{yyyy}{mm}{dd}/{time}{rand:6}'
      },
      activity: {
        content: '',
        activityName: '',
        shareDes: '',
        shareImg: '',
        activityImg: '',
        phoneImg:'',
        startDate: '',
        endDate: '',
        musicId: '',
        giftIds: '',
        goodsIds:'',
        shareGift: '',
        shareTimes: '',
        activityType: 2,
        auditRemarks: '',
        payStartDate:'',
        payEndDate:'',
        groupRate:'',
        pointsReturnMultiple:'',
        returnStartDate:'',
        returnEndDate:'',
        auditRemarks:'',
        skin:"1",
//        viewNum:0,
//        shareNum:0,
//        joinNum:0,
        discountLevel: [
          {
            nums: 0,
            level: 10
          }
        ]
      },
      Mainuploaderconfig: {
        maxSize: 2048,
        format: ['png', 'jpg', 'jpeg'],
        showUploadList: false,
        parent: 'activity',
        child: 'activityImg'
      },
      Phoneuploaderconfig: {
        maxSize: 2048,
        format: ['png', 'jpg', 'jpeg'],
        showUploadList: false,
        parent: 'activity',
        child: 'phoneImg'
      },
      uploaderconfig: {
        maxSize: 500,
        format: ['png', 'jpg', 'jpeg'],
        showUploadList: false,
        parent: 'activity',
        child: 'shareImg'
      },
      musicModel: false,
      GiftList: [],
      goodList:[],
      Group: [
        {
          nums: '',
          discount: ''
        }
      ],
      activeDate:false,
      payDate:false
    }
  },
  created () {
    this.getGiftList()
    this.getMusicList(1)

    this.http.get(this.$store.state.prefix + "/goods/page/1").then(res=>{
      if (res.error === false) {
        for (var i in res.result.records) {
          res.result.records[i].selected = false
        }
        this.goodList = res.result.records
      }
    })
  },
  methods: {
    selectModel(e){
      if(e == 2)
        this.isTeam = true;
      else
        this.isTeam = false;
    },
    changeDate (val, state) {
      if (state === 1) {
        this.activity.startDate = val
      } else {
        this.activity.endDate = val
      }
      if(this.activity.startDate != '' && this.activity.endDate != ''){
        this.activeDate = true;
      }
    },
    changePayDate (val, state) {
      if(val < this.activity.startDate || val > this.activity.endDate) {
        this.$Notice.error({title: 'æ”¯ä»˜æ—¶é—´é”™è¯¯', desc: 'æ”¯ä»˜æ—¶é—´å¿…é¡»åœ¨æ´»åŠ¨æ—¶é—´å†…',duration:3})
        return false;
      }
      if (state === 1) {
        this.activity.payStartDate = val
      } else {
        this.activity.payEndDate = val
      }
      if(this.activity.payStartDate != '' && this.activity.payEndDate != ''){
        this.payDate = true;
      }
    },
    changeReturnDate (val, state) {
      if(val < this.activity.payStartDate || val > this.activity.payEndDate) {
        this.$Notice.error({title: 'ç§¯åˆ†ç¿»å€æ—¶é—´é”™è¯¯', desc: 'ç§¯åˆ†ç¿»å€æ—¶é—´å¿…é¡»åœ¨æ”¯ä»˜æ—¶é—´å†…',duration:3})
        return false;
      }
      if (state === 1) {
        this.activity.returnStartDate = val
      }
      else {
        this.activity.returnEndDate = val
      }
    },
    Ueditorready (editor) {},
    Ueditorinput (obj) {
      this.activity.content = obj.content
      this.Ueditorconfig.info = obj
    },
    getMusicList (pageNo) {
      this.http.get(this.$store.state.prefix + '/music/page/' + (pageNo || 1)).then(res => {
        if (res.error === false) {
          this.musicList = res.result.records
        }
      })
    },
    saveActivity () {
      this._setGiftsId();
      this._setGoodsId();
      if (!this._setdiscountLevel()) return;
      if (!this._checkParamAndExecute()) return;
    },
    _setdiscountLevel () {
      var discountLevel = '[';
      var me = this;
      var isSuccess = true;
      this.Group.forEach(function (item, index) {
        if (index === 0) {
          if (me.util.isNull(item.nums) || me.util.isNull(item.discount)) {
            me.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™å›¢è´­ä¼˜æƒ ',duration:3})
            isSuccess = false
          }
        }
        if (item.discount > 10 || item.discount < 0) {
          me.$Notice.error({title: 'å›¢è´­ä¼˜æƒ é”™è¯¯', desc: 'ç¬¬' + (parseInt(index) + 1) + 'é¡¹æŠ˜æ‰£ä¿¡æ¯éœ€åœ¨0-10ä¹‹é—´',duration:3})
          isSuccess = false
        }
        if (index > 0) {
          if (~~(item.nums) <= ~~(me.Group[index - 1].nums)) {
            me.$Notice.error({title: 'å›¢è´­ä¼˜æƒ é”™è¯¯', desc: 'ç¬¬' + (parseInt(index) + 1) + 'é¡¹äººæ•°éœ€å¤§äºŽä¸Šä¸€é¡¹',duration:3})
            isSuccess = false
          }
          discountLevel += ','
        }
        discountLevel += '{"mans":'+item.nums + ',"discount":' + item.discount +'}'
      });
      this.activity.discountLevel = discountLevel+"]";
      if(isSuccess)
        return true;
      else
        return false;
    },
    _setGoodsId () {
      var list = [];
      for (var j in this.goodList) {
        if (this.goodList[j].selected === true) {
          list.push(this.goodList[j].id)
        }
      }
      this.activity.goodsIds = list.join(',')
    },
    _setGiftsId () {
      var giftList = [];
      for (var j in this.GiftList) {
        if (this.GiftList[j].selected === true) {
          giftList.push(this.GiftList[j].id)
        }
      }
      this.activity.giftIds = giftList.join(',')
    },
    _checkParamAndExecute () {
      var ai = JSON.parse(JSON.stringify(this.activity))
      console.log(ai)
      if (!ai.activityName) {
        this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™æ´»åŠ¨åç§°'})
        return false
      }
      if (!ai.shareDes) {
        this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™æ´»åŠ¨åˆ†äº«ä»‹ç»'})
        return false
      }
      if (!ai.shareImg) {
        this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·ä¸Šä¼ åˆ†äº«ç¼©ç•¥å›¾'})
