<template>
	<div class="giftadd">
		<div class="content-title">
      <div class="titlename">
      	<span>æ–°å¢žç¤¼å“</span>
      </div>
      <div class="titellink">
      <router-link to="/gift" class="innerbtnlink">ç¤¼å“åˆ—è¡¨</router-link>
      </div>
 	</div>
  <div class="content">
    <Form ref="GiftRule" :model="Gift" class="gifteditform" :rules="GiftRule" :label-width="100">
       <Form-item label="ç¤¼å“åç§°" prop="giftName">
            <Input v-model="Gift.giftName" placeholder="è¯·è¾“å…¥"></Input>
       </Form-item>
       <Form-item label="ç¤¼å“å›¾ç‰‡" class="text-left">
           <uploader :config="uploaderconfig"> </uploader>
           <input type="hidden" v-model="Gift.giftImg">
       </Form-item>
       <Form-item label="å›¾ç‰‡é¢„è§ˆ">
         <a :href="murl + Gift.giftImg" target="_blank" v-if="Gift.giftImg">
           <img :src="murl+Gift.giftImg" alt="" class="giftimgthumb" style="width:200px;height:200px;">
         </a>
      </Form-item>
       <Form-item label="ç¤¼å“ä»‹ç»">
            <Input type="textarea" v-model="Gift.giftDesc" :autosize="{minRows: 2,maxRows: 5}" placeholder="è¯·è¾“å…¥..."></Input>
       </Form-item>
       <Form-item>
            <Button type="primary" @click="handleSubmit('GiftRule')">æäº¤</Button>
            <Button type="ghost" style="margin-left: 8px" @click="handleReset('GiftRule')">é‡ç½®</Button>
        </Form-item>
    </Form>
  </div>
	</div>
</template>

<script type="text/ecmascript-6">
import uploader from '../Util/Uploader'
export default {
  name: 'GiftAdd',
  data () {
    return {
      Gift: {
        id: '',
        giftName: '',
        giftImg: '',
        giftDesc: ''
      },
      GiftRule: {
        giftName: [
          {required: true, message: 'ç¤¼ç‰©åç§°ä¸èƒ½ä¸ºç©º', trigger: 'blur'}
        ],
        giftImg: [
          {required: 'true', message: 'å›¾ç‰‡å¿…é¡»ä¸Šä¼ '}
        ]
      },
      uploaderconfig: {
        maxSize: 5120,
        format: ['jpg', 'png', 'jpeg'],
        showUploadList: false,
        parent: 'Gift',
        child: 'giftImg'
      }
    }
  },
  components: { uploader },
  methods: {
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          if (this.Gift.id === '') {
            delete this.Gift.id
          }
          this.http.post(this.$store.state.prefix + '/gift', this.Gift).then(res => {
            if (res.error === false) {
              this.$Message.success('ä¿å­˜æˆåŠŸ')
              this.router.push('/gift')
            }
          })
        } else {
          this.$Message.error('éªŒè¯å¤±è´¥')
        }
      })
    },
    handleReset (name) {
      this.$refs[name].resetFields();
    }
  }
}
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
.gifteditform
  width:60%
  margin-top:30px
.giftimgthumb
  width:100%
  height:auto
</style>



