              legend: {
                  data:['å•†å“äº¤æ˜“','è‡ªåŠ©è´­ä¹°','ä¼šå‘˜æ³¨å†Œ','æ´»åŠ¨äº¤æ˜“','è´¦æˆ·å……å€¼','è´¦æˆ·ç»­è´¹']
              },
