<template>
<div class="activitylist">
	<div class="content-title">
      <div class="titlename">
      	<span>å‚å›¢ç”¨æˆ·</span>
      </div>
      <div class="titellink">
      <router-link to="/activity" class="innerbtnlink">æ´»åŠ¨åˆ—è¡¨</router-link>
      </div>
 	</div>
 	<div class="content">
   <Table border :columns="activityteamColumns" :data="activityteamData" class="activityteamtable"></Table>
    <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
            <Page :total="activitypager.total" :current="activitypager.current" @on-change="changePage"></Page>
        </div>
    </div>
 	</div>
  <div class="hoverShowImg" v-if="isHover" :style="{left:Left+'px',top:Top+'px'}">
    <img :src="currentImg" width="100%" height="100%">
  </div>
    </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'ActivityTeam',
  data () {
    return {
      activityteamColumns: [
        {
          title: 'åºå·',
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: 'å›¢é•¿å§“å',
          key: 'nickName',
          render (row) {
            if(row.realName) {
              return row.realName
            } else {
              return row.nickName
            }
          }
        },
        {
          title: 'å›¢é•¿ç”µè¯',
          key: 'phone'
        },
        {
          title: 'å›¢å‘˜æ•°',
          key: 'groupNum'
        },
        {
          title: 'äº¤æ˜“äººæ•°',
          key: 'payNum'
        },
        {
          title: 'å›¢é•¿ç¦åˆ©',
          key: 'groupPoints'
        },
        {
          title: 'æ‰€èŽ·ç§¯åˆ†',
          key: 'allPayPoints'
        },
        {
          title: 'äº¤æ˜“é¢',
          key: 'allPayAmount'
        },
        {
          title: 'åˆ›å»ºæ—¶é—´',
          key: 'joinDate'
        },
        {
          title: 'æ“ä½œ',
          key: 'action',
          render (row) {
            return '<i-button type="text" size="small" @click = "viewmenber(row.groupId)">æŸ¥çœ‹å›¢å‘˜</i-button>'
          }
        }
      ],
      activityteamData: [],
      isHover:false,
      Left:0,
      Top:0,
      currentImg:'',
      activitypager: {
        pages: 1,
        current: 1,
        total:1
      }
    }
  },
  created () {
    this.getActivityTeam()
  },
  methods: {
    getActivityTeam(){
      var query = this.util.getQuery(location.hash)
      this.http.get(this.$store.state.prefix + '/activity/getAllGroupInfo/' + query.id).then(res => {
        if(res.error === false) {
          if(res.result){
            this.activitypager = res.result
            for(var i in res.result){
              res.result[i].joinDate=this.util.getFormatDate(res.result[i].joinDate)
            }
            this.activityteamData = res.result
          }
          
        } else {
          this.$Notice.error("èŽ·å–å¤±è´¥");
        }
      })
    },
    viewmenber(id) {
      var querys = this.util.getQuery(location.hash)
      this.router.push({path: '/activity/teamuser', query: {id: id,activityid: querys.id}})
    },
    changePage() {
      this.getActivityTeam(this.activitypager.current)
    },
  }
}
</script>

<style  lang='stylus' rel="stylesheet/stylus">

.activityteamtable
  .activitydesc
  	max-width:250px
  .copylink
  	font-size:0.8em
  	text-decoration:none
  	color:#6bbef2

.ivu-table
  td,th
    text-align:center
    .activityavater
      width:100px
      height:auto
      max-height:100px
.activitylistavater
  max-height:100px
  width:100%
</style>



