<template>
<div class="goodslist">
	<div class="content-title">
      <div class="titlename">
      	<span>å•†å“åˆ—è¡¨</span>
      </div>
      <div class="titellink">
      <router-link to="/goods/add" class="innerbtnlink">æ–°å¢žå•†å“</router-link>
      </div>
 	</div>
 	<div class="content">
    <Table border :columns="goodslistColumns" :data="goodslistData" class="goodslistable"></Table>
    <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
            <Page :total="goodspager.total" :page-size="goodspager.size" :current="goodspager.current" @on-change="changePage"></Page>
        </div>
    </div>
 	</div>
  <Modal v-model="delmodal" width="360">
        <p slot="header" style="color:#f60;text-align:center">
            <Icon type="information-circled"></Icon>
            <span>åˆ é™¤ç¡®è®¤</span>
        </p>
        <div style="text-align:center">
            <p>æ­¤å•†å“åˆ é™¤åŽï¼Œå…¶å…³è”æ´»åŠ¨å°†æ— æ³•ç»§ç»­æ”¯ä»˜æ­¤å•†å“ã€‚</p>
            <p>æ˜¯å¦ç»§ç»­åˆ é™¤ï¼Ÿ</p>
        </div>
        <div slot="footer">
            <Button type="error" size="large" long :loading="modal_loading" @click="del">åˆ é™¤</Button>
        </div>
    </Modal>
</div>

</template>

<script type="text/ecmascript-6">
  export default {
    name: 'Goods',
    data () {
      return {
        delmodal: false,
        modal_loading: false,
        willdelid: '',
        goodslistColumns: [
          {
            title: 'åºå·',
            type: 'index',
            width: 60,
            align: 'center'
          },
          {
            title: 'å•†å“åç§°',
            key: 'goodsName'
          },
          {
            title: 'å•†å“å›¾ç‰‡',
            key: 'goodsImg',
            render (row) {
              return '<img class="goodslistavater" :src="murl + row.goodsImg" v-if="row.goodsImg"/>'
            }
          },
          {
            title: 'å•†å“ä»·æ ¼',
            key: 'goodsPrice'
          },
          {
            title: 'å…‘æ¢ç§¯åˆ†',
            key: 'maxPoints'
          },
          {
            title: 'é”€å”®é‡',
            key: 'saleNum'
          },
          {
            title: 'åº“å­˜',
            key: 'storageNum'
          },{
            title: 'æŽ’åº',
            key: 'sort'
          },
          {
            title: 'ä¸Šæž¶çŠ¶æ€',
            key: 'goodsStatus',
            render (row) {
              if(row.goodsStatus == 1){
                return 'ä¸Šæž¶'
              } else {
                return 'ä¸‹æž¶'
              }
            }
          },{
            title: 'å•†å“ç±»åž‹',
            key: 'goodsType',
            render (row) {
             return row.goodsType==1?'çŽ°é‡‘':row.goodsType==2?'ç§¯åˆ†':'çŽ°é‡‘+ç§¯åˆ†'
            }
          },
          {
            title: 'æ“ä½œ',
            key: 'action',
            render (row) {
              return '<i-button type="text" size="small" @click = "update(row.id)">ä¿®æ”¹</i-button>' +
                '<i-button type="text" size="small" @click="setdelid(row.id)">åˆ é™¤</i-button>'
            }
          }
        ],
        goodslistData: [],
        goodspager: {
          total:1,
          pages: 1,
          current: 1,
          size:12
        }
      }
    },
    created () {
      this.getGoodsList(1)
    },
    methods: {
      getGoodsList (pageNo) {
        this.http.get(this.$store.state.prefix +'/goods/page/' + (pageNo || 1)).then(res => {
          if (res.error === false) {
            this.goodspager = res.result
            console.log(this.goodspager)
            for (var i in res.result.records) {
              res.result.records[i].goodsImg = res.result.records[i].goodsImg.split(',')[0]
            }
            this.goodslistData = res.result.records
          }
        })
      },
      changePage (pageno) {
        this.getGoodsList(pageno)
      },
      update (id) {
        this.router.push({path: '/goods/edit', query: {id: id}});
      },
      setdelid (id) {
        this.willdelid = id
        this.delmodal = true
      },
      del () {
        if (!this.willdelid) {
          return
        }
        this.modal_loading = true
        this.http.post(this.$store.state.prefix + '/goods/delete', {id: this.willdelid}).then(res => {
          if (res.error === false) {
            this.delmodal = false
            this.modal_loading = false
            this.willdelid = ""
            this.$Message.success('åˆ é™¤æˆåŠŸ')
            this.getGoodsList(1);
          }
        })
      }
    }
  }
</script>

<style lang='stylus' rel="stylesheet/stylus">
.goodslistable
  td
  	vertical-align:middle
  .goodsdesc
    max-width:200px
    overflow:hidden

.ivu-table
  td,th
    text-align:center
    padding:0px
    .ivu-table-cell
      padding:0px
      .goodslistavater
        width:90%
        height:auto
        margin:0px auto
        max-height:100px
</style>



