<template>
	<div class="musicadd">
		<div class="content-title">
      <div class="titlename">
      	<span>ä¿®æ”¹éŸ³ä¹</span>
      </div>
      <div class="titellink">
      <router-link to="/music" class="innerbtnlink">éŸ³ä¹åº“</router-link>
        <router-link to="/music/add" class="innerbtnlink">æ–°å¢žéŸ³ä¹</router-link>
      </div>
 	</div>
  <div class="content">
    <Form ref="musicRule" :model="music" class="musiceditform" :rules="musicRule" :label-width="100">
       <Form-item label="éŸ³ä¹åç§°" prop="name">
            <Input v-model="music.name" placeholder="è¯·è¾“å…¥"></Input>
       </Form-item>
       <Form-item label="ä¸Šä¼ éŸ³ä¹" class="text-left">
           <uploader :config="uploaderconfig"> </uploader>
           <input type="hidden" v-model="music.url">
       </Form-item>
       <Form-item label="éŸ³ä¹é¢„è§ˆ">
         <audio :src="murl+music.url" v-if="music.url" class="musicpanel" autoplay="true" controls="true" />
      </Form-item>
       <Form-item>
            <Button type="primary" @click="handleSubmit('formValidate')">æäº¤</Button>
            <Button type="ghost" style="margin-left: 8px" @click="handleReset('formValidate')">å–æ¶ˆ</Button>
        </Form-item>
    </Form>
  </div>
	</div>
</template>

<script type="text/ecmascript-6">
import uploader from '../Util/Uploader'
export default {
  name: 'MusicAdd',
  data () {
    return {
      music: {
        id: '',
        name: '',
        url: ''
      },
      musicRule: {
        name: [
          {required: true, message: 'éŸ³ä¹åç§°å¿…é¡»', trigger: 'blur'}
        ],
        url: [
          {required: 'true', message: 'éŸ³ä¹åœ°å€'}
        ]
      },
      uploaderconfig: {
        maxSize: 5120,
        format: ['mp3', 'ogg'],
        showUploadList: false,
        parent: 'music',
        child: 'url'
      }
    }
  },
  created () {
    this.getMusicById();
  },
  components: { uploader },
  methods: {
    getMusicById () {
      var query = this.util.getQuery(location.hash);
      this.http.get(this.$store.state.prefix + '/music/' + query.id).then(res => {
        if (res.error === false) {
          this.music = res.result;
        }
      })
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          if (this.music.id === '') {
            delete this.music.id
          }
          this.http.put(this.$store.state.prefix + '/music', this.music).then(res => {
            if (res.error === false) {
              this.$Message.success('ä¿å­˜æˆåŠŸ')
              this.router.push('/music')
            }
          })
        } else {
          this.$Message.error('éªŒè¯å¤±è´¥')
        }
      })
    }
  }
}
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
.musiceditform
  width:60%
  margin-top:30px
  .musicpanel
    height:30px
.musicimgthumb
  width:100%
  height:auto

</style>



