<template>
  <div class="inviterlist">
    <div class="content-title" style="margin:5px;">
      <div class="titlename">
        <span>é‚€è¯·æŽ’è¡Œæ¦œ</span>
      </div>
      <div class="titellink">
        <Date-picker type="date" v-model="queryinfo.month" placeholder="æ‰€åœ¨æœˆä»½" format="yyyy/MM/dd" style="width: 30%;float:left"></Date-picker>
        <Select placeholder="æŸ¥è¯¢æ¡æ•°" v-model="queryinfo.limit" style="width:30%;float:left">
