<template>
<div class="orderlist">
	<div class="content-title">
      <!-- é¡µé¢çš„æ ‡é¢˜ -->

      <div class="titlename">
      	<span>è®¢å•åˆ—è¡¨</span>
      </div>
      <div class="titellink">
        <Select v-model="orderquery.payType" slot="prepend" style="width:25%;" placeholder="è®¢å•ç±»åž‹">
          <Option value="">æ‰€æœ‰è®¢å•</Option>
          <Option value="2">å•†å“è®¢å•</Option>
          <Option value="6">è‡ªåŠ©ä»˜æ¬¾</Option>
          <Option value="5">æ³¨å†Œä¼šå‘˜</Option>
          <Option value="4">è´¦æˆ·å……å€¼</Option>
        </Select>
        <Select v-model="orderquery.payStatus" slot="prepend" style="width:25%;" placeholder="æ”¯ä»˜çŠ¶æ€">
          <Option value="">æ‰€æœ‰çŠ¶æ€</Option>
          <Option value="1">æˆåŠŸ</Option>
          <Option value="2">æœªæˆåŠŸ</Option>
        </Select>
        <Input v-model="orderquery.nameOrPhone" placeholder="è¯·è¾“å…¥å…³é”®å­—"  style="width:20%"></Input>
        <Button type="primary" icon="ios-search" @click = "getOrderList(1)" >æŸ¥è¯¢</Button>
      </div>
 	</div>
 	<div class="content">
  <Table border :columns="orderlistColumns" :data="orderlistData" class="orderlistable"></Table>
    <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
            <Page :total="pager.total" :page-size="pager.size" :current="pager.current" @on-change="changePage($event)"></Page>
        </div>
    </div>
 	</div>
</div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'Orders',
  data () {
    return {
      orderquery:{
        payType: '',
        nameOrPhone: '',
        payStatus:''
      },
      orderlistColumns: [
        {
          title: 'åºå·',
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: 'è®¢å•å·',
          key: 'payNo',
          ellipsis:true
        },
        {
          title: 'ç”¨æˆ·å',
          key: 'name'
        },
        {
          title:"å¤´åƒ",
          key:'headImg',
          render (row) {
            return '<img :src="row.account.headImg" style="width:40px;height:40px;" v-if="row.account&&row.account.headImg"/>'
          }
        },
        {
          title: 'æŠ˜æ‰£ï¼ˆ%ï¼‰',
          key: 'discount',
          render (row) {
            if(row.payType==2){
              return (~~10*row.discount)
            }else{
              return row.discount
            }
          }
        },
        {
          title: 'æ”¯ä»˜é‡‘é¢',
          key: 'payAmount'
        },
        {
          title: 'æ”¯ä»˜ç§¯åˆ†',
          key: 'payPoints'
        },
        {
          title: 'æ”¯ä»˜çŠ¶æ€',
          key: 'payStatus',
          render (row) {
            return row.payStatus==1?'<Icon type="checkmark-round"></Icon>':'<Icon type="alert" color="red" title="æœªæˆåŠŸ"></Icon>'
          }
        },
        {
          title: 'æ”¯ä»˜æ—¶é—´',
          key: 'payDate'
        },
        {
          title: 'ä¿¡æ¯',
          key: 'remarks'
        }
      ],
      orderlistData: [],
      pager: {
        total: 1,
        size:12,
        current: 1
      }
    }
  },
  created () {
    this.getOrderList(1)
  },
  methods: {
    getOrderList (pageNo) {
      this.http.get(this.$store.state.prefix + '/pay/page/' + (pageNo || 1) + '?payType=' +this.orderquery.payType+'&nameOrPhone='+this.orderquery.nameOrPhone+"&payStatus="+this.orderquery.payStatus).then(res => {
        if (res.error === false) {
          this.pager = res.result
          this.orderlistData = res.result.records
          this.orderlistData.forEach(item=>{
            item.payDate = this.util.getFormatDate(item.payDate);
            item.name = item.account?(item.account.realName == ''?item.account.nickName:item.account.realName):'';
            item.headImg = item.account.headImg;
          })
        }
      })
    },
    changePage (e) {
      this.getOrderList(e)
    }
  }
}
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
</style>



