<template>
  <div class="musicadd">
    <div class="content-title">
      <div class="titlename">
        <span>æ–°å¢žéŸ³ä¹</span>
      </div>
      <div class="titellink">
        <router-link to="/music" class="innerbtnlink">éŸ³ä¹åº“</router-link>
      </div>
    </div>
    <div class="content">
      <Form ref="musicRule" :model="music" class="musiceditform" :rules="musicRule" :label-width="100">
        <Form-item label="éŸ³ä¹åç§°" prop="name">
          <Input v-model="music.name" placeholder="è¯·è¾“å…¥"></Input>
        </Form-item>
        <Form-item label="ä¸Šä¼ éŸ³ä¹" class="text-left">
          <uploader :config="uploaderconfig"> </uploader>
          <input type="hidden" v-model="music.url">
        </Form-item>
        <Form-item label="éŸ³ä¹é¢„è§ˆ">
          <audio :src="murl+music.url" v-if="music.url" class="musicpanel" autoplay="true" controls="true" />
        </Form-item>
        <Form-item>
          <Button type="primary" @click="handleSubmit('musicRule')">æäº¤</Button>
          <Button type="ghost" style="margin-left: 8px" @click="handleReset('musicRule')">å–æ¶ˆ</Button>
        </Form-item>
      </Form>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import uploader from '../Util/Uploader'
  export default {
    name: 'MusicAdd',
    data () {
      return {
        music: {
          name: '',
          url: ''
        },
        musicRule: {
          name: [
            {required: true, message: 'åç§°ä¸èƒ½ä¸ºç©º', trigger: 'blur'}
          ],
          url: [
            {required: true, message: 'éŸ³ä¹åœ°å€'}
          ]
        },
        uploaderconfig: {
          maxSize: 5120,
          format: ['mp3', 'ogg'],
          showUploadList: false,
          parent: 'music',
          child: 'url'
        }
      }
    },
    components: { uploader },
    methods: {
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.http.post(this.$store.state.prefix + '/music', this.music).then(res => {
              if (res.error === false) {
                this.$Message.success('ä¿å­˜æˆåŠŸ')
                this.router.push('/music')
              }
            })
          } else {
            this.$Message.error('éªŒè¯å¤±è´¥')
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields();
      }
    }
  }
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
  .musiceditform
    width:60%
    margin-top:30px
    .musicpanel
      height:30px
  .musicimgthumb
    width:100%
    height:auto

</style>



