<template>
 
<div class="emploeyelist">
<div class="content-title">
      <!-- é¡µé¢çš„æ ‡é¢˜ -->
      
      <div class="titlename">
      	<span>å‘˜å·¥åˆ—è¡¨</span>
      </div>
      <div class="titellink">
      <router-link to="/employee/add" class="innerbtnlink">æ·»åŠ å‘˜å·¥</router-link>
      </div>
 </div>
<div class="content">
	 <Table border :columns="employeelistColumns" :data="employeelistData"></Table>
   <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
            <Page :total="employeepager.total" :page-size="employeepager.size" :current="employeepager.current" @on-change="changePage"></Page>
        </div>
    </div>

</div>
 </div>

</template>

<script  type="text/ecmascript-6">
export default {
  name: 'Employee',
  data () {
    return {
      employeelistColumns: [
        {
          title: 'åºå·',
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: 'å¤´åƒ',
          key: 'cphoto',
          render (row) {
            return '<img class="employeeavater" :src="murl+row.cphoto"/>'
          }
        },
        {
          title: 'å§“å',
          key: 'employeeName'
        },
        {
          title: 'ç”µè¯',
          key: 'employeeTel'
        },
        {
          title: 'å®¢æˆ·æ•°',
          key: 'customerNum'
        },
        {
          title: 'æ“ä½œ',
          key: 'action',
          render () {
            return '<i-button type="text" size="small">ä¿®æ”¹</i-button><i-button type="text" size="small">åˆ é™¤</i-button>'
          }
        }

      ],
      employeelistData: [],
      employeepager: {
        total: 1,
        size:1,
        current: 1
      }
    }
  },
  created () {
    this.getEmployeeList(1)
  },
  methods: {
    getEmployeeList (pageNo) {
      this.http.get(this.$store.state.prefix + '/employee/page/' + pageNo || 1).then(res => {
        if (res.error === false) {
          this.employeepager = res.result
          this.employeelistData = res.result.records
        }
      })
    },
    changePage (e) {
      this.getEmployeeList(e)
    }
  }
}
</script>

<style lang='stylus' rel="stylesheet/stylus">
.employeelisttable
  tr
  	td
  	  vertical-align:middle
  	  font-size:0.9em
  	  text-align:center


.ivu-table
  td,th
    text-align:center
    .employeeavater
      display:inline-block
      width:40px
      height:40px
      border:1px solid #eee
      border-radius:3px
</style>



