<template>
<div class="musicinfo">
  <div class="content-title">
      <!-- é¡µé¢çš„æ ‡é¢˜ -->

      <div class="titlename">
        <span>éŸ³ä¹åº“</span>
      </div>
      <div class="titellink">
      <router-link to="/music/add" class="innerbtnlink">æ–°å¢žéŸ³ä¹</router-link>
      </div>
 </div>
 <div class="content">
  <Table border :columns="musiclistColumns" :data="musiclistData" class="musiclistable"></Table>
    <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
            <Page :total="musicpager.total" :page-size="musicpager.size" :current="musicpager.current" @on-change="changePage"></Page>
        </div>
    </div>
 </div>
</div>
</template>

<script  type="text/ecmascript-6">
export default {
  name: 'Music',
  data () {
    return {
      musiclistColumns: [
        {
          title: 'åºå·',
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: 'éŸ³ä¹åç§°',
          key: 'name'
        },
        {
          title: 'éŸ³ä¹åœ°å€',
          key: 'url',
          render (row) {
            return '<a :href="murl+row.url" target="_blank" v-text="row.url"></a>'
          }
        },
        {
          title: 'åˆ›å»ºæ—¶é—´',
          key: 'createDate'
        },
        {
          title: 'æ“ä½œ',
          key: 'action',
          render (row) {
            return '<i-button type="text" size="small" @click = "update(row.id)">ä¿®æ”¹</i-button>' +
              '<i-button type="text" size="small" @click="del(row.id)">åˆ é™¤</i-button>'
          }
        }
      ],
      musiclistData: [],
      musicpager: {
        total: 1,
        size:12,
        current: 1
      }
    }
  },
  created () {
    this.getMusicList(1)
  },
  methods: {
    getMusicList (pageNo) {
      this.http.get(this.$store.state.prefix + '/music/page/' + (pageNo || 1)).then(res => {
        if (res.error === false) {
          this.musicpager = res.result
          this.musiclistData = res.result.records
          this.musiclistData.forEach(item=>{
            item.createDate = this.util.changeDateToTime(item.createDate);
          })
        }
      })
    },
    changePage (e) {
      this.getMusicList(e)
    },
    update (id) {
      this.router.push({path: '/music/edit', query: {id: id}});
    },
    del (id) {
      this.http.post(this.$store.state.prefix + '/music/delete', {id: id}).then(res => {
        if (res.error === false) {
          this.$Message.success('åˆ é™¤æˆåŠŸ')
          this.getMusicList(1);
        }
      })
    }
  }
}
</script>
<style lang='stylus' rel="stylesheet/stylus">
.ivu-table
  td,th
    text-align:center
</style>



