<template>
  <div class="goodsadd">
    <div class="content-title">
      <div class="titlename">
        <span>ä¿®æ”¹å•†å“</span>
      </div>
      <div class="titellink">
        <router-link to="/goods" class="innerbtnlink">å•†å“åˆ—è¡¨</router-link>
        <router-link to="/goodsAdd" class="innerbtnlink">æ–°å¢žå•†å“</router-link>
      </div>
    </div>
    <div class="">
      <Form ref="formValidate" :model="Goods" class="goodseditform" :rules="GoodsRule" :label-width="100">
        <Form-item label="å•†å“åç§°" prop="goodsName">
          <Input v-model="Goods.goodsName" placeholder="è¯·è¾“å…¥"></Input>
        </Form-item>
        <Form-item label="å•†å“å›¾ç‰‡" class="text-left">
          <uploader :config="uploaderconfig"> </uploader>
          <input type="hidden" v-model="Goods.goodsImg">
        </Form-item>
        <Form-item label="å›¾ç‰‡é¢„è§ˆ">
          <a  v-if="Goods.goodsImg" class="goodsimgwrapper" v-for="gg,index in Goods.goodsImg.split(',')">
           <img :src="murl+gg" alt="" class="goodsimgthumb" style="width:250px;height:250px;">
           <div class="delbtns">
             <img src="/static/images/del.png" alt="" @click="delThisPic(index)">
           </div>
         </a>
        </Form-item>
        <Form-item label="å•†å“ç±»åž‹" prop="goodsType">
          <Select v-model="Goods.goodsType" style="width:100%" @on-change="setGoodsType">
            <Option value="1">çŽ°é‡‘</Option>
            <Option value="2">ç§¯åˆ†</Option>
            <Option value="3">çŽ°é‡‘åŠ ç§¯åˆ†</Option>
          </Select>
        </Form-item>
        <Form-item label="å•†å“ä»·æ ¼" prop="goodsPrice" v-if="showPrice">
          <Input type="text" v-model="Goods.goodsPrice" number ></Input>
        </Form-item>
        <Form-item label="æœ€å¤§å…‘æ¢ç§¯åˆ†" prop="maxPoints" v-if="showPoint">
          <Input type="text" v-model="Goods.maxPoints" number ></Input>
        </Form-item>
      
        <Form-item label="åº“å­˜"  prop="storageNum">
          <Input type="text" v-model="Goods.storageNum" number></Input>
        </Form-item>
        <Form-item label="çŠ¶æ€" class="text-left"  prop="goodsStatus">
          <Select v-model="Goods.goodsStatus" style="width:200px">
            <Option value="1">ä¸Šæž¶</Option>
            <Option value="2">ä¸‹æž¶</Option>
          </Select>
        </Form-item>
        <Form-item label="æŽ’åº">
          <Input type="text" v-model="Goods.sort" number></Input>
        </Form-item>
        <Form-item label="å•†å“ä»‹ç»">
         <!--  <Input type="textarea" v-model="Goods.goodsDesc" :autosize="{minRows: 2,maxRows: 5}" placeholder="è¯·è¾“å…¥..."></Input> -->
           <div class="addcontent ueditor-wrapper">
            <ueditor :value="defaultMSg" :config="Ueditorconfig" @input="Ueditorinput" v-on:ready="Ueditorready"></ueditor>
        </div>
        </Form-item>
        <Form-item>
          <Button type="primary" @click="handleSubmit('formValidate')">æäº¤</Button>
          <Button type="ghost" style="margin-left: 8px" @click="handleReset('formValidate')">å–æ¶ˆ</Button>
        </Form-item>
      </Form>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import uploader from '../Util/Uploader'
  import ueditor from '../Ueditor'
  export default {
    name: 'GoodsAdd',
    data () {
      return {
        showPrice:false,
        showPoint:false,
        Goods: {
          goodsName: '',
          goodsImg: '',
          goodsPrice: '',
          maxPoints: '',
          sort: 0,
          storageNum: '',
          goodsStatus: "1",
          goodsType: "1",
          goodsDesc: '',
          goodsDate: (new Date().getTime())
        },
        GoodsRule: {
          goodsName: [
            {required: true, message: 'å•†å“åç§°ä¸èƒ½ä¸ºç©º', trigger: 'blur'}
          ],
          goodsPrice: [
            {type: 'number', min: 0.01, message: 'ä»·æ ¼åªèƒ½å¤§äºŽ0', trigger: 'blur'}
          ],
          maxPoints: [
            {type: 'number', min: 0, message: 'æœ€å¤§å…‘æ¢ç§¯åˆ†åªèƒ½å¤§äºŽ0', trigger: 'blur'}
          ],
          storageNum: [
            {type: 'integer', min: 0, message: 'åº“å­˜é‡åªèƒ½ä¸ºæ­£æ•´æ•°', trigger: 'blur'}
          ]
        },
        uploaderconfig: {
          maxSize: 5120,
          format: ['jpg', 'png', 'jpeg'],
          showUploadList: false,
          parent: 'Goods',
          child: 'goodsImg',
          multiple:true
        },
        defaultMSg: '',
        Ueditorconfig: {
          initialFrameWidth: null,
          initialFrameHeight: 320,
          info: {},
          imageUrl: '//up.qbox.me/',
          imageActionName: 'uploadimage',
          imageFieldName: 'file',
          imageMaxSize: 2048000,
          imageAllowFiles: ['.jpg', '.png', '.jpeg'],
          imageCompressEnable: true,
          imageCompressBorder: 1600,
          imageInsertAlign: 'none',
          imageUrlPrefix: '//oolds3geo.bkt.clouddn.com/',
          imagePathFormat: 'upload/image/{yyyy}{mm}{dd}/{time}{rand:6}'
        }
      }
    },
    components: { uploader ,ueditor },
    created(){

      var query = this.util.getQuery(location.hash);
      this.http.get(this.$store.state.prefix +  "/goods/"+query.id).then(res=>{
        if (res.error === false) {
          this.Goods = res.result;
          this.Goods.goodsType= String(this.Goods.goodsType)
          this.Goods.goodsStatus= String(this.Goods.goodsStatus)
          this.Goods.goodsDesc = this.util.escapeToHtml(this.Goods.goodsDesc)
          this.defaultMSg=res.result.goodsDesc
          this.setGoodsType(this.Goods.goodsType)
        }
      });
    },
    methods: {
      setGoodsType(val){
        this.showPrice = false;
        this.showPoint = false;
        switch(~~val){
          case 1:this.showPrice = true;break;
          case 2:this.showPoint = true;break;
          case 3:this.showPoint = true;this.showPrice=true;break;
        }
      },
      handleSubmit (name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            if (this.Goods.id === '') {
              delete this.Goods.id
            }
            this.http.put(this.$store.state.prefix + '/goods', this.Goods).then(res => {
              if (res.error === false) {
                this.$Message.success('ä¿®æ”¹æˆåŠŸ')
                this.router.push('/goods')
              }
            })
          } else {
            this.$Message.error('éªŒè¯å¤±è´¥')
          }
        })
      },
      handleReset (name) {
        this.$refs[name].resetFields();
      },
      Ueditorready (editor) {},
      Ueditorinput (obj) {
        console.log(obj)
        this.Goods.goodsDesc = obj.content
        this.Ueditorconfig.info = obj
      },
      delThisPic (index) {
      var temp=this.Goods.goodsImg.split(",");
      temp.splice(index,1)
        this.Goods.goodsImg=temp.join(",")
      }
    }
  }
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
  .goodseditform
    width:60%
    margin-top:30px
  .goodsimgwrapper
    display:block
    position:relative
    .delbtns
      position:absolute
      right:10px
      bottom:50%
      height:25px
      width:25px
      img
        width:100%
    .goodsimgthumb
      width:100%
      height:auto
      
  .ueditor-wrapper
    width:166%
</style>



