UE.plugin.register('serverparam', function (){

    var me = this,
        serverParam = {};

    return {
        commands:{
            /**
             * ä¿®æ”¹æœåŠ¡å™¨æäº¤çš„é¢å¤–å‚æ•°åˆ—è¡¨,æ¸…é™¤æ‰€æœ‰é¡¹
             * @command serverparam
             * @method execCommand
             * @param { String } cmd å‘½ä»¤å­—ç¬¦ä¸²
             * @example
             * ```javascript
             * editor.execCommand('serverparam');
             * editor.queryCommandValue('serverparam'); //è¿”å›žç©º
             * ```
             */
            /**
             * ä¿®æ”¹æœåŠ¡å™¨æäº¤çš„é¢å¤–å‚æ•°åˆ—è¡¨,åˆ é™¤æŒ‡å®šé¡¹
             * @command serverparam
             * @method execCommand
             * @param { String } cmd å‘½ä»¤å­—ç¬¦ä¸²
             * @param { String } key è¦æ¸…é™¤çš„å±žæ€§
             * @example
             * ```javascript
             * editor.execCommand('serverparam', 'name'); //åˆ é™¤å±žæ€§name
             * ```
             */
            /**
             * ä¿®æ”¹æœåŠ¡å™¨æäº¤çš„é¢å¤–å‚æ•°åˆ—è¡¨,ä½¿ç”¨é”®å€¼æ·»åŠ é¡¹
             * @command serverparam
             * @method execCommand
             * @param { String } cmd å‘½ä»¤å­—ç¬¦ä¸²
             * @param { String } key è¦æ·»åŠ çš„å±žæ€§
             * @param { String } value è¦æ·»åŠ å±žæ€§çš„å€¼
             * @example
             * ```javascript
             * editor.execCommand('serverparam', 'name', 'hello');
             * editor.queryCommandValue('serverparam'); //è¿”å›žå¯¹è±¡ {'name': 'hello'}
             * ```
             */
            /**
             * ä¿®æ”¹æœåŠ¡å™¨æäº¤çš„é¢å¤–å‚æ•°åˆ—è¡¨,ä¼ å…¥é”®å€¼å¯¹å¯¹è±¡æ·»åŠ å¤šé¡¹
             * @command serverparam
             * @method execCommand
             * @param { String } cmd å‘½ä»¤å­—ç¬¦ä¸²
             * @param { Object } key ä¼ å…¥çš„é”®å€¼å¯¹å¯¹è±¡
             * @example
             * ```javascript
             * editor.execCommand('serverparam', {'name': 'hello'});
             * editor.queryCommandValue('serverparam'); //è¿”å›žå¯¹è±¡ {'name': 'hello'}
             * ```
             */
            /**
             * ä¿®æ”¹æœåŠ¡å™¨æäº¤çš„é¢å¤–å‚æ•°åˆ—è¡¨,ä½¿ç”¨è‡ªå®šä¹‰å‡½æ•°æ·»åŠ å¤šé¡¹
             * @command serverparam
             * @method execCommand
             * @param { String } cmd å‘½ä»¤å­—ç¬¦ä¸²
             * @param { Function } key è‡ªå®šä¹‰èŽ·å–å‚æ•°çš„å‡½æ•°
             * @example
             * ```javascript
             * editor.execCommand('serverparam', function(editor){
             *     return {'key': 'value'};
             * });
             * editor.queryCommandValue('serverparam'); //è¿”å›žå¯¹è±¡ {'key': 'value'}
             * ```
             */

            /**
             * èŽ·å–æœåŠ¡å™¨æäº¤çš„é¢å¤–å‚æ•°åˆ—è¡¨
             * @command serverparam
             * @method queryCommandValue
             * @param { String } cmd å‘½ä»¤å­—ç¬¦ä¸²
             * @example
             * ```javascript
             * editor.queryCommandValue( 'serverparam' ); //è¿”å›žå¯¹è±¡ {'key': 'value'}
             * ```
             */
            'serverparam':{
                execCommand:function (cmd, key, value) {
                    if (key === undefined || key === null) { //ä¸ä¼ å‚æ•°,æ¸…ç©ºåˆ—è¡¨
                        serverParam = {};
                    } else if (utils.isString(key)) { //ä¼ å…¥é”®å€¼
                        if(value === undefined || value === null) {
                            delete serverParam[key];
                        } else {
                            serverParam[key] = value;
                        }
                    } else if (utils.isObject(key)) { //ä¼ å…¥å¯¹è±¡,è¦†ç›–åˆ—è¡¨é¡¹
                        utils.extend(serverParam, key, true);
                    } else if (utils.isFunction(key)){ //ä¼ å…¥å‡½æ•°,æ·»åŠ åˆ—è¡¨é¡¹
                        utils.extend(serverParam, key(), true);
                    }
                },
                queryCommandValue: function(){
                    return serverParam || {};
                }
            }
        }
    }
});


// plugins/insertfile.js
/**
 * æ’å…¥é™„ä»¶
 */
UE.plugin.register('insertfile', function (){

    var me = this;

    function getFileIcon(url){
        var ext = url.substr(url.lastIndexOf('.') + 1).toLowerCase(),
            maps = {
                "rar":"icon_rar.gif",
                "zip":"icon_rar.gif",
                "tar":"icon_rar.gif",
                "gz":"icon_rar.gif",
                "bz2":"icon_rar.gif",
                "doc":"icon_doc.gif",
                "docx":"icon_doc.gif",
                "pdf":"icon_pdf.gif",
                "mp3":"icon_mp3.gif",
                "xls":"icon_xls.gif",
                "chm":"icon_chm.gif",
                "ppt":"icon_ppt.gif",
                "pptx":"icon_ppt.gif",
                "avi":"icon_mv.gif",
                "rmvb":"icon_mv.gif",
                "wmv":"icon_mv.gif",
                "flv":"icon_mv.gif",
                "swf":"icon_mv.gif",
                "rm":"icon_mv.gif",
                "exe":"icon_exe.gif",
                "psd":"icon_psd.gif",
                "txt":"icon_txt.gif",
                "jpg":"icon_jpg.gif",
                "png":"icon_jpg.gif",
                "jpeg":"icon_jpg.gif",
                "gif":"icon_jpg.gif",
                "ico":"icon_jpg.gif",
                "bmp":"icon_jpg.gif"
            };
        return maps[ext] ? maps[ext]:maps['txt'];
    }

    return {
        commands:{
            'insertfile': {
                execCommand: function (command, filelist){
                    filelist = utils.isArray(filelist) ? filelist : [filelist];

                    var i, item, icon, title,
                        html = '',
                        URL = me.getOpt('UEDITOR_HOME_URL'),
                        iconDir = URL + (URL.substr(URL.length - 1) == '/' ? '':'/') + 'dialogs/attachment/fileTypeImages/';
                    for (i = 0; i < filelist.length; i++) {
                        item = filelist[i];
                        icon = iconDir + getFileIcon(item.url);
                        title = item.title || item.url.substr(item.url.lastIndexOf('/') + 1);
                        html += '<p style="line-height: 16px;">' +
                            '<img style="vertical-align: middle; margin-right: 2px;" src="'+ icon + '" _src="' + icon + '" />' +
                            '<a style="font-size:12px; color:#0066cc;" href="' + item.url +'" title="' + title + '">' + title + '</a>' +
                            '</p>';
                    }
                    me.execCommand('insertHtml', html);
                }
            }
        }
    }
});




// plugins/xssFilter.js
/**
 * @file xssFilter.js
 * @desc xssè¿‡æ»¤å™¨
 * @author robbenmu
 */

UE.plugins.xssFilter = function() {

	var config = UEDITOR_CONFIG;
	var whitList = config.whitList;

	function filter(node) {

		var tagName = node.tagName;
		var attrs = node.attrs;

		if (!whitList.hasOwnProperty(tagName)) {
			node.parentNode.removeChild(node);
			return false;
		}

		UE.utils.each(attrs, function (val, key) {

			if (whitList[tagName].indexOf(key) === -1) {
				node.setAttr(key);
			}
		});
	}

	// æ·»åŠ inserthtml\pasteç­‰æ“ä½œç”¨çš„è¿‡æ»¤è§„åˆ™
	if (whitList && config.xssFilterRules) {
		this.options.filterRules = function () {

			var result = {};

			UE.utils.each(whitList, function(val, key) {
				result[key] = function (node) {
					return filter(node);
				};
			});

			return result;
		}();
	}

	var tagList = [];

	UE.utils.each(whitList, function (val, key) {
		tagList.push(key);
	});

	// æ·»åŠ inputè¿‡æ»¤è§„åˆ™
	//
	if (whitList && config.inputXssFilter) {
		this.addInputRule(function (root) {

			root.traversal(function(node) {
				if (node.type !== 'element') {
					return false;
				}
				filter(node);
			});
		});
	}
	// æ·»åŠ outputè¿‡æ»¤è§„åˆ™
	//
	if (whitList && config.outputXssFilter) {
		this.addOutputRule(function (root) {

			root.traversal(function(node) {
				if (node.type !== 'element') {
					return false;
				}
				filter(node);
			});
		});
	}

};


