<template>
<div class="withdrawlist">
	<div class="content-title">
      <div class="titlename">
      	<span>ç”¨æˆ·æçŽ°</span>
      </div>
      <div class="titellink">
         <Input v-model="querytext" placeholder="è¯·è¾“å…¥å…³é”®å­—"  style="width:35%"></Input>
        <Button type="primary" icon="ios-search" @click = "getwithdrawList(1)" >æŸ¥è¯¢</Button>
      </div>
 	</div>
 	<div class="content">
    <Table border :columns="listColumns" :data="listData" class="giftlistable"></Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="pager.total" :page-size="pager.size" :current="pager.current" @on-change="changePage"></Page>
      </div>
    </div>
 	</div>
</div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'Withdraw',
  data () {
    return {
      listColumns: [
        {
          title: 'åºå·',
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: 'ç”¨æˆ·å',
          key: 'realName',
          render(row){
            if(row.realName == ''
)              return "<span>{{row.nickName}}</span>"
            else
              return "<span>{{row.realName}}</span>"
          }
        },
        {
          title: 'å¤´åƒ',
          key: 'headImg',
          render (row) {
            return '<img class="goodslistavater" :src="row.headImg" style="width:40px;height:40px;"/>'
          }
        },
        {
          title: 'æçŽ°å†…å®¹',
          key: 'remarks'
        },
        {
          title: 'æçŽ°æ¯”çŽ‡(%)',
          key: 'toCashRate'
        },
        {
          title: 'æçŽ°é‡‘é¢',
          key: 'withdrawAmount'
        },
        {
          title: 'æçŽ°æ‰‹ç»­è´¹',
          key: 'withdrawFactorage'
        },
        {
          title: 'æçŽ°ç§¯åˆ†',
          key: 'withdrawPoints'
        },{
          title: 'æçŽ°çŠ¶æ€',
          key: 'withdrawStatus',
          render (row){
            if(row.withdrawStatus === 1) {
              return 'æˆåŠŸ'
            }else {
              return 'æœªæˆåŠŸ'
            }
          }
        },
        {
          title: 'æçŽ°æ—¶é—´',
          key: 'successDate'
        }
      ],
      listData: [],
      pager: {
        total: 1,
        pages: 1,
        current:1,
        size:12
      },
      querytext:''
    }
  },
  created () {
    this.getwithdrawList(1)
  },
  methods: {
    getwithdrawList (pageNo) {
      this.http.get(this.$store.state.prefix + '/withdraw/page/' + (pageNo || 1)+"?withdrawType=1&nameOrPhone="+this.querytext).then(res => {
        if (res.error === false) {
          this.pager = res.result
          this.listData = res.result.records
          this.listData.forEach(item=>{
            item.successDate = this.util.getFormatDate(item.successDate);
          })
        }
      })
    },
    changePage (e) {
      this.getwithdrawList(e)
    }
  }
}
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
.withdrawlisttable
  .withdraworigin
  	max-width:200px
  .withdrawavater
  	width:40px
  	height:auto
  	max-height:40px
  .withdrawstatus
  	width:20px
  	height:20px
</style>



