<template>
<div class="giftlist">
	<div class="content-title">
      <!-- é¡µé¢çš„æ ‡é¢˜ -->

      <div class="titlename">
      	<span>ç¤¼å“åˆ—è¡¨</span>
      </div>
      <div class="titellink">
      <router-link to="/gift/add" class="innerbtnlink">æ–°å¢žç¤¼å“</router-link>
      </div>
 	</div>
 	<div class="content">
    <Table border :columns="giftlistColumns" :data="giftlistData" class="giftlistable"></Table>
    <div style="margin: 10px;overflow: hidden">
        <div style="float: right;">
            <Page :total="giftpager.total" :page-size="giftpager.size" :current="giftpager.current" @on-change="changePage($event)"></Page>
        </div>
    </div>
 	</div>
</div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'Gift',
  data () {
    return {
      giftlistColumns: [
        {
          title: 'åºå·',
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: 'ç¤¼å“åç§°',
          key: 'giftName'
        },
        {
          title: 'ç¤¼å“å›¾ç‰‡',
          key: 'giftImg',
          className: 'giftavater-wrapper',
          render (row) {
            return '<img class="giftavater" :src="murl + row.giftImg"/>'
          }
        },
        {
          title: 'ç¤¼å“ç®€ä»‹',
          key: 'giftDesc'
        },
        {
          title: 'æ“ä½œ',
          key: 'action',
          render (row) {
            return '<i-button type="text" size="small" @click = "update(row.id)">ä¿®æ”¹</i-button>' +
              '<i-button type="text" size="small" @click="del(row.id)">åˆ é™¤</i-button>'
          }
        }
      ],
      giftlistData: [],
      giftpager: {
        total: 1,
        size:1,
        current: 1
      }
    }
  },
  created () {
    this.getGiftList(1)
  },
  methods: {
    getGiftList (pageNo) {
      this.http.get(this.$store.state.prefix + '/gift/page/' + pageNo || 1).then(res => {
        if (res.error === false) {
          this.giftpager = res.result
          this.giftlistData = res.result.records
        }
      })
    },
    changePage (e) {
      this.getGiftList(e)
    },
    update (id) {
      this.router.push({path: '/gift/edit', query: {id: id}});
    },
    del (id) {
      this.http.post(this.$store.state.prefix + '/gift/delete', {id: id}).then(res => {
        if (res.error === false) {
          this.$Message.success('åˆ é™¤æˆåŠŸ');
          this.getGiftList(1);
        }
      })
    }
  }
}
</script>

<style lang='stylus' rel="stylesheet/stylus">
.ivu-table
  td,th
    text-align:center
    .giftavater
      width:100px
    	height:auto
    	max-height:100px
</style>



