<template>
<div class="weixinpay">
   <Steps :current="currentStep">
        <Step title="é€‰æ‹©é‡‘é¢" content=""></Step>
        <Step title="æ‰«ç å……å€¼" content=""></Step>
        <Step title="å……å€¼å®Œæˆ" content=""></Step>
    </Steps>
    <div class="stepContent">
      <div class="chooseMoney" v-if="currentStep==0">
        <ul>
          <li v-for="pm in payMoney" :class="{active:pm.active,coustom:pm.coustom}" @click="selectMoney(pm)">
            <span v-text="pm.money" v-if="!pm.coustom"></span>
            <input v-else type="number" v-model="pm.money" min="100" step="50" placeholder="è¯·è¾“å…¥">
          </li>
        </ul>
      </div>
      <div class="payQrCode text-center"  v-if="currentStep==1">
        <img :src="'http://qr.liantu.com/api.php?w=200&m=5&text='+code_url" alt="" v-if="code_url">
        <Row>
        <span v-text="'å……å€¼é‡‘é¢:'+realMoney+'å…ƒ'" style="font-size:1.5em"></span></br>
         <Icon type="ios-information-outline"></Icon>è¯·ä½¿ç”¨å¾®ä¿¡æ‰«ä¸€æ‰«è¿›è¡Œå……å€¼ä»˜æ¬¾</Row>
      </div>
      <div class="payStatus" v-if="currentStep==2">
          <Icon type="ios-checkmark-outline" size="100" color="#44b549"></Icon>
          <br>
          å……å€¼æˆåŠŸ
      </div>
    </div>
    <div class="stepOperate text-right">
        <Button type="ghost"  size="small" v-if="currentStep>0&&currentStep<2" @click="switchStep(false)">ä¸Šä¸€æ­¥</Button>
        <Button type="info"  size="small" v-if="currentStep<=2&&(!stepdone)" @click="switchStep(true)">
       ä¸‹ä¸€æ­¥</Button>
       <Button type="info"  size="small" v-if="currentStep<=2&&(stepdone)" @click="chargeOk()">
       å®Œæˆ</Button>
       

    </div>
</div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'WeixinPay',
  props: ['wrapper', 'company'],
  data () {
    return {
      payMoney: [
        {
          money: 100,
          active: true,
          coustom: false
        },
        {
          money: 500,
          active: false,
          coustom: false
        },
        {
          money: 1000,
          active: false,
          coustom: false
        },
        {
          money: 2000,
          active: false,
          coustom: false
        },
        {
          money: 5000,
          active: false,
          coustom: false
        },
        {
          money: null,
          active: false,
          coustom: true
        }
      ],
      currentStep: 0,
      stepdone: false,
      realMoney: 100,
      code_url:'',
      repeateNum:0
    }
  },
  created () {
  },
  mounted () {
  },
  methods: {
    selectMoney (pm) {
      for (var i in this.payMoney) {
        this.payMoney[i].active = false
      }
      pm.active = true
    },
    switchStep (type) {
      if (type === true) {
        if (this.currentStep === 0) {
          var realMoney = 0
          for (var i in this.payMoney) {
            if (this.payMoney[i].active === true) {
              realMoney = this.payMoney[i].money
              break
            }
          }
          if (realMoney < 10 || isNaN(realMoney)) {
            this.$Notice.error({
              title: 'é”™è¯¯',
              desc: 'å……å€¼é‡‘é¢å¿…é¡»å¤§äºŽ10'
            })
            return
          }
          this.realMoney = realMoney
          var param = {
            payType: 4,
            payAmount: Number(this.realMoney),
            companyId :this.company.id,
            businessId: 0,
            goodsId: 0,
            companyId:this.util.getCookie("companyId")
          }
          this.http.post(this.$store.state.prefix + '/pay' , param).then (res => {
            if (res.error === false) {
              this.code_url = res.result.code_url
              this.currentStep ++
              var _this=this
              setTimeout(function(){
                  _this.checkStatus(res.result.out_trade_no)
              },3000)
              
            } else {
              this.$Message.error(res.msg)
            }
          })

        }
        if(this.currentStep === 2){
         this.stepdone = true
        }
      } else {
        this.stepdone = false
        this.currentStep -= 1
      }
    },
    chargeOk() {
       var _this=this
          for(var i=0;i<100;i++){
            if(_this.$parent[this.wrapper] === undefined){
              _this = _this.$parent
              
            }else{
              _this.$parent[this.wrapper] = false
              this.stepdone = false
              this.currentStep = 0
              _this.$parent.getCompanyinfo()
              break
            }
          }
    },
    checkStatus (out_trade_no) {
        if(this.wrapper==false)return
        this.http.post(this.$store.state.prefix + '/pay/getByOutTradeNo', {outTradeNo: out_trade_no}).then(res => {
        if (res.error === false) {
            if(res.result.payStatus === 1){
              this.currentStep ++
              this.switchStep(true)
            }else{
              var _this=this
              if(this.repeateNum<200){
                setTimeout(function(){
                  _this.checkStatus(out_trade_no)
                },1000)
              }
              
            }
        }
      })
    }
  }
}
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
.stepContent
  padding:20px 5px
  .chooseMoney
    ul
      width:80%
      margin:0px auto
      li
        padding: 5px 10px
        border:1px solid #ccc
        border-radius:3px
        text-align:center
        display:inline-block
        margin:0px 5px 5px 5px
        cursor:pointer
        min-width:60px
      li.active
        background:#63c168
        color:#fff
      li.coustom
        padding:0px
        width:100px
        border:0px
        input
          width:100%
          height:30px
          border:1px solid #ccc
          border-radius:3px
          padding:0px 3px
  .payQrCode
    text-align:center
    img
      margin:0px auto
  .payStatus
    text-align:center

</style>



