<template>
  <div class="union">
  <div class="content-title">
    <div class="titlename">
        <span>èµ„é‡‘æµæ°´</span>
      </div>
      <div class="titellink">
      </div>
      </div>
      <div class="content">
        <Row class="recordquery">
          <Col span="6">
            <Date-picker v-model="queryinfo.date" type="daterange" format="yyyy-MM-dd" placeholder="é€‰æ‹©æŸ¥è¯¢å‘¨æœŸ" style="width: 100%"></Date-picker>
          </Col>
          <Col span="4">
             <Radio-group v-model="queryinfo.queryType" type="button">
                <Radio label="1"><span>æ”¯å‡º</span></Radio>
                <Radio label="2"><span>æ”¶å…¥</span></Radio>
            </Radio-group>
          </Col>
          <Col span="4">
             <Radio-group  v-model="queryinfo.queryStatus" type="button">
                <Radio label="1"><span>æˆåŠŸ</span></Radio>
                <Radio label="2"><span>å¤±è´¥</span></Radio>
            </Radio-group>
          </Col>
           <Col span="4">
             <Input  v-model="queryinfo.nameOrPhone" placeholder="å§“åæˆ–è€…æ‰‹æœºå·"></Input>
          </Col>
           <Col span="4">
             <Button type="primary" @click="getBalanceRecord()">æŸ¥è¯¢</Button>
          </Col>
        </Row>
        <Row class="recordcontent">
          <Table border :columns="balanceColumn" :data="balanceData"></Table>
          <div style="margin: 10px;overflow: hidden">
              <div style="float: right;">
                  <Page :total="pager.total" :page-size="pager.size" :current="pager.current" @on-change="getBalanceRecord"></Page>
              </div>
          </div>

        </Row>
      </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'BalanceRecord',
  data () {
    return {
      queryinfo:{
        date:[],
        queryType:1,
        queryStatus:1,
        nameOrPhone:''
      },
      balanceColumn:[
        {
            title: 'åºå·',
            type: 'index',
            width: 60,
            align: 'center'
          },
          {
            title: 'ç”¨æˆ·',
            key: 'realName',
            render(row){
              return row.realName||row.nickName||row.account.realName||row.account.nickName
            }
          },
          {
            title: 'è¯´æ˜Ž',
            key: 'remarks'
          },
          {
            title: 'é‡‘é¢',
            key: 'payAmount',
            render(row){
              if(row.payAmount != undefined){
                return row.payAmount
              }else {
                return row.withdrawAmount
              }
            }
          },
          {
            title: 'ç§¯åˆ†',
            key: 'action',
            render(row){
              if(row.payPoints != undefined){
                return row.payPoints
              }else {
                return row.withdrawPoints
              }
            }
          },{
            title: 'æ—¶é—´',
            key: 'payDate',
            render(row){
              if(row.payDate){
                return row.payDate
              }else{
                return row.successDate
              }
            }
          },
          {
            title: 'çŠ¶æ€',
            key: 'payStatus',
            render(row){
              if(row.payStatus){
                  return row.payStatus==1?'æˆåŠŸ':'å¤±è´¥'
              }else{
                  return row.withdrawStatus==1?'æˆåŠŸ':'å¤±è´¥'
              }
            }
          },
          {
            title: 'ç»“ä½™',
            key: 'balance'
          }
      ],
      balanceData:[],
      pager:{
        current:1,
        total:1,
        size:12
      }

    }
  },
  methods: {
    getBalanceRecord(pageno){
      if(pageno){
        this.pager.current=pageno
      }else {
        this.pager.current=1
      }
      var param={
        startDate:this.util.getDate(this.queryinfo.date[0]),
        endDate:this.util.getDate(this.queryinfo.date[1]),
        nameOrPhone:this.queryinfo.nameOrPhone
      }
      var url=""
      if(this.queryinfo.queryType==1){
        //æ”¯å‡º-æçŽ°
        param.withdrawStatus=this.queryinfo.queryStatus
        url="/withdraw/page/"
      }else{
        param.payStatus=this.queryinfo.queryStatus
        url="/pay/page/"
      }
      this.http.get(this.$store.state.prefix+url+this.pager.current+'?a=1'+this.util.parseParam(param)).then(res=>{
        if(res.error==false){
          res.result.records.forEach((item)=>{
            if(item.payDate){
              item.payDate=this.util.getFormatDate(item.payDate)
            }else{
              item.successDate=this.util.getFormatDate(item.successDate)
            }
          })
          this.balanceData=res.result.records
          this.pager.current=res.result.current
          this.pager.size=res.result.size
          this.pager.count=res.result.count
        }else{
          this.$Message.error(res.msg)
        }
      })
    }
  },
  watch:{
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='stylus' rel="stylesheet/stylus">
.recordquery
  margin-top:10px
.recordcontent
  margin-top:10px
</style>



