<script type="text/ecmascript-6">
import weixinPay from '../Util/WeixinPay'
import renew from '../Util/Renew'
export default {
  name: 'Home',
  components:{ weixinPay, renew},
  data () {
    return {
      tabs:'basic',
      weixinpayModal: false,
      binduserModal:false,
      withdrawModal: false,
      renewModal:false,
      rate:this.$store.state.rate,//æçŽ°æ‰‹ç»­è´¹
      companyinfo: {
        account:{
          realName:'',
          nickName:'',
          headImg:'',
          phone:''
        },
        companyName: '',
        companyDesc: '',
        companyTel: '',
        companyLogo: '',
        smsTel:'',
        toCashRate: 0,
        employeeRate: '',
        sharePoints:0,
        shareMax: 1,
        registerMoney:0,
        registerPoints:0,
        selfReturn:0,
        oneReturn:0,
        secondReturn:0,
        overDate: '2017-12-30'
      },
      Group: [
        {
          mans: '',
          discount: ''
        }
      ],
      weixinpayModal: false,
      employeeColumn:[
        {
           type: 'selection',
