           align: 'center'
        },
        {
          title: 'æ˜µç§°',
          key: 'nickName',
          render (row) {
            if (row.realName) {
              return row.realName
            } else {
              return row.nickName
            }
          }
        },
        {
          title: 'å¤´åƒ',
          key: 'headImg',
          render (row) {
            return '<img :src="row.headImg" v-if="row.headImg" style="width:40px;height:40px" alt="">'
          }
        },
        {
          title: 'æ“ä½œ',
          key: 'action',
          render (row) {
            return '<i-button type="text" size="small">é€‰å®š</i-button>'
          }
        }
      ],
      employeeData:[],
      pager:{
        total: 1,
        current: 1
      },
      withdrawMoney:0,
      queryinfo:{
        companyId:this.util.getCookie("companyId"),
        withdrawType:'2',
        withdrawStatus:'2',
        current:1,
        size:12,
        total:1
      },
      withdrawColumns:[
        {
          title: 'åºå·',
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: 'æ”¶æ¬¾äºº',
