        },
        {
          title: 'ç”³è¯·æ—¶é—´',
          key: 'applyDate'
        },
        {
          title: 'é¢„ç»“ç®—',
          key: 'dealDate',
          sortable: true
        },{
        title:'çŠ¶æ€',
        key:'withdrawStatus',
        render(row){
          if(row.withdrawStatus==1){ 
            return "å®Œæˆ"
          }else {
            return 'ç”³è¯·ä¸­'
          }
        }
        }
      ],
      withdrawlist:[]
    }
  },
  created () {
    var query = this.util.getQuery(location.hash);
    this.tabs=query.name
    this.getWithDrawList()
    if (this.$store.state.companyId) {
      this.getCompanyinfo()
