<template>
<div class="withdrawlist">
	<div class="content-title">
      <div class="titlename">
      	<span>ç”¨æˆ·æçŽ°</span>
      </div>
      <div class="titellink">
         <Input v-model="querytext" placeholder="è¯·è¾“å…¥å…³é”®å­—"  style="width:35%"></Input>
        <Button type="primary" icon="ios-search" @click = "getwithdrawList(1)" >æŸ¥è¯¢</Button>
      </div>
 	</div>
 	<div class="content">
    <Table border :columns="listColumns" :data="listData" class="giftlistable"></Table>
    <div style="margin: 10px;overflow: hidden">
      <div style="float: right;">
        <Page :total="pager.total" :page-size="pager.size" :current="pager.current" @on-change="changePage"></Page>
      </div>
    </div>
 	</div>
  <Modal 
      v-model="inputcaptcha"
      ok-text="false"
      cancelText="false"
      title="éªŒè¯æ”¾æ¬¾" >
      æ­£åœ¨ç¡®è®¤{{currentWithdraw.realName}}å‘èµ·çš„{{currentWithdraw.withdrawAmount}}æçŽ°åˆ’æ¬¾ã€‚
      <Input placeholder="è¯·è¾“å…¥éªŒè¯ç " v-model="smscountdown.captcha">
        <Button slot="append" type="primary" icon="paper-airplane" v-if="smscountdown.enable" @click="getCaptcha(currentWithdraw)">å‘é€</Button>
        <Button slot="append" icon="ios-clock-outline" v-else>{{smscountdown.timer}}ç§’åŽå‘é€</Button>
    </Input>
    <div slot="footer">
            <Button type="error" size="large" @click="doLoan">ç¡®è®¤æ”¾æ¬¾</Button>
        </div>

    </Modal>
</div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'Withdraw',
  data () {
    return {
      listColumns: [
        {
          title: 'åºå·',
          type: 'index',
          width: 60,
          align: 'center'
        },
        {
          title: 'ç”¨æˆ·å',
          key: 'realName',
          render(row){
            if(row.realName == '')
              return "<span>{{row.nickName}}</span>"
            else
              return "<span>{{row.realName}}</span>"
          }
        },
        {
          title: 'å¤´åƒ',
          key: 'headImg',
          render (row) {
            return '<img class="goodslistavater" :src="row.headImg" style="width:40px;height:40px;"/>'
          }
        },
        {
          title: 'æçŽ°å†…å®¹',
          key: 'remarks'
        },
        {
          title: 'æçŽ°æ¯”çŽ‡(%)',
          key: 'toCashRate'
        },
        {
          title: 'æçŽ°é‡‘é¢',
          key: 'withdrawAmount'
        },
        {
          title: 'æçŽ°æ‰‹ç»­è´¹',
          key: 'withdrawFactorage'
        },
        {
          title: 'æçŽ°ç§¯åˆ†',
          key: 'withdrawPoints'
        },
        {
          title: 'ç”³è¯·æ—¶é—´',
          key: 'applyDate'
        },
        {
          title: 'æ“ä½œ',
          key: 'withdrawStatus',
          render (row){
            return '<i-button type="text" size="small" @click="getCaptcha(row)">ç¡®å®šæ”¾æ¬¾</i-button>' +
                    '<i-button type="text" size="small" >æ‹’ç»</i-button>'
          }
        }
      ],
      listData: [],
      pager: {
        total: 1,
        pages: 1,
        current:1,
        size:12
      },
      querytext:'',
      inputcaptcha:false,
      smscountdown:{
        timer:localStorage.getItem('timer')||0,
        enable:localStorage.getItem('enable')||true,
        captcha:''
      },
      currentWithdraw:{}//å½“å‰æçŽ°ä¿¡æ¯
    }
  },
  created () {
    this.getwithdrawList(1)
    if((Date.now()-localStorage.getItem("now"))/1000>localStorage.getItem("timer")){
      this.smscountdown.enable=true
      this.smscountdown.timer=0
      localStorage.setItem('timer',0)
      localStorage.setItem('enable',true)
    }else{
      var _this=this
      this.smscountdown.enable=false
      this.smscountdown.timer=~~localStorage.getItem("timer")
      this.smscountdown.interval=setInterval(function(){
          if(_this.smscountdown.timer>0){
            _this.smscountdown.timer--
            localStorage.setItem('timer',_this.smscountdown.timer)
            localStorage.setItem('now',Date.now())
          }else{
            clearInterval(_this.smscountdown.interval)
            _this.smscountdown.enable=true
            _this.smscountdown.timer=0

            localStorage.setItem('timer',0)
            localStorage.setItem('enable',true)
          }
      },1000)
    }
  },
  methods: {
    getwithdrawList (pageNo) {
      this.http.get(this.$store.state.prefix + '/withdraw/page/' + (pageNo || 1)+"?withdrawStatus=2&withdrawType=1&nameOrPhone="+this.querytext).then(res => {
        if (res.error === false) {
          this.pager = res.result
          this.listData = res.result.records
          this.listData.forEach(item=>{
            item.applyDate = this.util.getFormatDate(item.applyDate);
          })
        }
      })
    },
    changePage (e) {
      this.getwithdrawList(e)
    },
    getCaptcha(row){
      this.currentWithdraw=row
      this.inputcaptcha=true
      if(this.smscountdown.enable==true){
      //åˆ¤æ–­ä¸Šä¸€æ¬¡çš„å€’è®¡æ—¶æ˜¯å¦å®Œæ¯•ã€‚
        this.http.post(this.$store.state.prefix+'/withdraw/sendAuthCode',{withdrawId:row.id,companyId:this.util.getCookie("companyId")}).then(res=>{
          if(res.error==false){
            this.$Message.success("éªŒè¯ç å‘é€æˆåŠŸ")
            this.smscountdown.enable=false
            this.smscountdown.timer=60
            localStorage.setItem('timer',60)
            localStorage.setItem('enable',false)
            var _this=this
            this.smscountdown.interval=setInterval(function(){
                if(_this.smscountdown.timer>0){
                  _this.smscountdown.timer--
                  localStorage.setItem('timer',_this.smscountdown.timer)
                  localStorage.setItem('now',Date.now())
                }else{
                  clearInterval(_this.smscountdown.interval)
                  _this.smscountdown.enable=true
                  _this.smscountdown.timer=0

                  localStorage.setItem('timer',0)
                  localStorage.setItem('enable',true)
                }
            },1000)
          }else{
            this.$Message.error(res.msg)
          }
        })
      }else{
        this.$Message.info("è¯·æŸ¥çœ‹æ‰‹æœºè¾“å…¥éªŒè¯ç ")
      }
      
    },
    doLoan(){
      var param={
        randomStr:this.smscountdown.captcha,
        companyId:this.util.getCookie("companyId"),
        withdrawId:this.currentWithdraw.id
      }
      if(this.smscountdown.captcha.length==0){
        this.$Message.error("è¯·è¾“å…¥éªŒè¯ç ")
        return
      }
      if(this.smscountdown.captcha.length!=6){
        this.$Message.error("éªŒè¯ç ä¸º6ä½æ•°å­—")
        return
      }
      this.http.post(this.$store.state.prefix+'/withdraw/applyUserWithdraw',param).then(res=>{
        if(res.error==false){
          this.$Message.success("æçŽ°åˆ’æ¬¾æˆåŠŸ")
          this.inputcaptcha=false
          clearInterval(_this.smscountdown.interval)
          this.smscountdown.enable=true
          this.smscountdown.timer=0

          localStorage.setItem('timer',0)
          localStorage.setItem('enable',true)
          this.smscountdown.captcha=''
          this.getwithdrawList(1)
        }else{
          this.$Message.error(res.msg)
        }
      })
    }
  }
}
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
</style>



