<script type="text/ecmascript-6">
  import ueditor from '../Ueditor'
  import uploader from '../Util/Uploader'
  export default {
    components: { ueditor, uploader },
    name: 'ActivityAdd',
    mounted () {
      this.over = true;
    },
    data () {
      return {
        isTeam:true,
        musicList: [],
        defaultMSg: '',
        Ueditorconfig: {
          initialFrameWidth: null,
          initialFrameHeight: 320,
          info: {},
          imageUrl: '//up.qbox.me/',
          imageActionName: 'uploadimage',
          imageFieldName: 'file',
          imageAllowFiles: ['.jpg', '.png', '.jpeg'],
          imageCompressEnable: true,
          imageCompressBorder: 1600,
          imageInsertAlign: 'none',
          imageUrlPrefix: '//oolds3geo.bkt.clouddn.com/',
          imagePathFormat: 'upload/image/{yyyy}{mm}{dd}/{time}{rand:6}'
        },
        activity: {
          content: '',
          activityName: '',
          shareDes: '',
          shareImg: '',
          activityImg: '',
          phoneImg:'',
          startDate: '',
          endDate: '',
          musicId: '',
          giftIds: '',
          goodsIds:'',
          shareGift: '',
          shareTimes: '',
          activityType: 2,
          groupRate:0,
          auditRemarks: '',
          payStartDate:'',
          payEndDate:'',
          pointsReturnMultiple:'',
          returnStartDate:'',
          returnEndDate:'',
          auditRemarks:'',
//        viewNum:0,
//        shareNum:0,
//        joinNum:0,
          discountLevel: [
            {
              mans: 0,
              level: 10
            }
          ]
        },
        Mainuploaderconfig: {
          maxSize: 2048,
          format: ['png', 'jpg', 'jpeg'],
          showUploadList: false,
          parent: 'activity',
          child: 'activityImg'
        },
        Phoneuploaderconfig: {
          maxSize: 2048,
          format: ['png', 'jpg', 'jpeg'],
          showUploadList: false,
          parent: 'activity',
          child: 'phoneImg'
        },
        uploaderconfig: {
          maxSize: 500,
          format: ['png', 'jpg', 'jpeg'],
          showUploadList: false,
          parent: 'activity',
          child: 'shareImg'
        },
        musicModel: false,
        GiftList: [],
        goodList:[],
        Group: [
          {
            mans: '',
            discount: ''
          }
        ],
        activeDate:false,
        payDate:false,
        selectGoods:[]
      }
    },
    created () {
      /*this.getGiftList();*/
      this.getMusicList(1);

      var query = this.util.getQuery(location.hash);
      this.http.get(this.$store.state.prefix +  "/activity/"+query.id).then(res=>{
        if (res.error === false) {
          this.activity = res.result;
          this.Group = JSON.parse(this.activity.discountLevel.replace(/&quot;/g,'"'));
          this.defaultMSg = this.util.escapeToHtml(this.activity.content);
          this.activity.content = this.defaultMSg
          this.activity.startDate = this.util.getFormatDate(this.activity.startDate)
          this.activity.endDate = this.util.getFormatDate(this.activity.endDate)
          this.activity.endDate = this.util.getFormatDate(this.activity.endDate)
          this.activity.payStartDate = this.util.getFormatDate(this.activity.payStartDate)
          this.activity.payEndDate = this.util.getFormatDate(this.activity.payEndDate)
          this.activity.returnStartDate = this.util.getFormatDate(this.activity.returnStartDate)
          this.activity.returnEndDate = this.util.getFormatDate(this.activity.returnEndDate)

          console.log(this.activity)
          this.selectGoods = this.activity.goodsIds.split(",")
          this.selectGift = this.activity.giftIds.split(",")
        }
      }).then(()=>{
        this.http.get(this.$store.state.prefix + "/goods/page/1").then(res=>{
          if (res.error === false) {
            console.log(this.selectGoods)
            for (var i of res.result.records) {
              for(var goods of this.selectGoods){
                if(i.id == ~~goods)
                  i.selected = true
                else
                  i.selected = false
              }
            }
            this.goodList = res.result.records
          }
        })


        this.http.get(this.$store.state.prefix + '/gift/page/1').then(res => {
            if (res.error === false) {
              for (var i of res.result.records) {
                i.selected = false
                for(var gift of this.selectGift) {
                  if(i.id === ~~gift) {
                    i.selected = true
                  }
                }
              }
              this.GiftList = res.result.records
            }
          })
      })

    },
    methods: {
      selectModel(e){
        if(e == 2)
          this.isTeam = true;
        else
          this.isTeam = false;
      },
      changeDate (val, state) {
        if (state === 1) {
          this.activity.startDate = val
        } else {
          this.activity.endDate = val
        }
        if(this.activity.startDate != '' && this.activity.endDate != ''){
          this.activeDate = true;
        }
      },
      changePayDate (val, state) {
        if(val < this.activity.startDate || val > this.activity.endDate) {
          this.$Notice.error({title: 'æ”¯ä»˜æ—¶é—´é”™è¯¯', desc: 'æ”¯ä»˜æ—¶é—´å¿…é¡»åœ¨æ´»åŠ¨æ—¶é—´å†…',duration:3})
          return false;
        }
        if (state === 1) {
          this.activity.payStartDate = val
        } else {
          this.activity.payEndDate = val
        }
        if(this.activity.payStartDate != '' && this.activity.payEndDate != ''){
          this.payDate = true;
        }
      },
      changeReturnDate (val, state) {
        if(val < this.activity.payStartDate || val > this.activity.payEndDate) {
          this.$Notice.error({title: 'ç§¯åˆ†ç¿»å€æ—¶é—´é”™è¯¯', desc: 'ç§¯åˆ†ç¿»å€æ—¶é—´å¿…é¡»åœ¨æ”¯ä»˜æ—¶é—´å†…',duration:3})
          return false;
        }
        if (state === 1) {
          this.activity.returnStartDate = val
        }
        else {
          this.activity.returnEndDate = val
        }
      },
      Ueditorready (editor) {},
      Ueditorinput (obj) {
        this.activity.content = obj.content
        this.Ueditorconfig.info = obj
      },
      getMusicList (pageNo) {
        this.http.get(this.$store.state.prefix + '/music/page/' + (pageNo || 1)).then(res => {
          if (res.error === false) {
            this.musicList = res.result.records
          }
        })
      },
      saveActivity () {
        this._setGiftsId();
        this._setGoodsId();
        if (!this._setdiscountLevel()) return;
        if (!this._checkParamAndExecute()) return;
      },
      _setdiscountLevel () {
        var discountLevel = '[';
        var me = this;
        var isSuccess = true;
        this.Group.forEach(function (item, index) {
          if (index === 0) {
            if (me.util.isNull(item.mans) || me.util.isNull(item.discount)) {
              me.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™å›¢è´­ä¼˜æƒ ',duration:3})
              isSuccess = false
            }
          }
          if (item.discount > 10 || item.discount < 0) {
            me.$Notice.error({title: 'å›¢è´­ä¼˜æƒ é”™è¯¯', desc: 'ç¬¬' + (parseInt(index) + 1) + 'é¡¹æŠ˜æ‰£ä¿¡æ¯éœ€åœ¨0-10ä¹‹é—´',duration:3})
            isSuccess = false
          }
          if (index > 0) {
            if (~~(item.mans) <= ~~(me.Group[index - 1].mans)) {
              me.$Notice.error({title: 'å›¢è´­ä¼˜æƒ é”™è¯¯', desc: 'ç¬¬' + (parseInt(index) + 1) + 'é¡¹äººæ•°éœ€å¤§äºŽä¸Šä¸€é¡¹',duration:3})
              isSuccess = false
            }
            discountLevel += ','
          }
          discountLevel += '{"mans":'+item.mans + ',"discount":' + item.discount +'}'
        });
        this.activity.discountLevel = discountLevel+"]";
        if(isSuccess)
          return true;
        else
          return false;
      },
      _setGoodsId () {
        var list = [];
        for (var j in this.goodList) {
          if (this.goodList[j].selected === true) {
            list.push(this.goodList[j].id)
          }
        }
        this.activity.goodsIds = list.join(',')
      },
      _setGiftsId () {
        var giftList = [];
        for (var j in this.GiftList) {
          if (this.GiftList[j].selected === true) {
            giftList.push(this.GiftList[j].id)
          }
        }
        this.activity.giftIds = giftList.join(',')
      },
      _checkParamAndExecute () {
        var ai = JSON.parse(JSON.stringify(this.activity))
        console.log(ai)
        if (!ai.activityName) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™æ´»åŠ¨åç§°'})
          return false
        }
        if (!ai.shareDes) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™æ´»åŠ¨åˆ†äº«ä»‹ç»'})
          return false
        }
        if (!ai.shareImg) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·ä¸Šä¼ åˆ†äº«ç¼©ç•¥å›¾'})
          return false
        }
        if (!ai.activityImg) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·ä¸Šä¼ æ´»åŠ¨å¤§å›¾'})
          return false
        }
        if (!ai.startDate) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·é€‰æ‹©æ´»åŠ¨å¼€å§‹æ—¶é—´'})
          return false
        }
        if (!ai.endDate) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·é€‰æ‹©æ´»åŠ¨ç»“æŸæ—¶é—´'})
          return false
        }
        if (!ai.payStartDate) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·é€‰æ‹©æ”¯ä»˜å¼€å§‹æ—¶é—´'})
          return false
        }
        if (!ai.payEndDate) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·é€‰æ‹©æ”¯ä»˜ç»“æŸæ—¶é—´'})
          return false
        }
        if (!ai.returnStartDate) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·é€‰æ‹©ç§¯åˆ†ç¿»å€å¼€å§‹æ—¶é—´'})
          return false
        }
        if (!ai.returnEndDate) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·é€‰æ‹©ç§¯åˆ†ç¿»å€ç»“æŸæ—¶é—´'})
          return false
        }
        if (this.util.isNull(ai.shareGift)) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·é€‰æ‹©æ˜¯å¦æ‰“å¼€åˆ†äº«å¾—ç¤¼'})
          return false
        }
        if ((ai.shareGift | 0) === 1) {
          if (!ai.giftIds) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·é€‰æ‹©åˆ†äº«ç¤¼å“'})
            return false
          }
        }
        if (this.util.isNull(ai.discountLevel)) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·è®¾ç½®å›¢è´­ä¼˜æƒ '})
          return false
        }
        if (!ai.pointsReturnMultiple) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·è¾“å…¥ç§¯åˆ†è¿”è¿˜å€æ•°'})
          return false
        }
        if (!ai.content) {
          this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·ç¼–è¾‘æ´»åŠ¨å†…å®¹è¯¦æƒ…'})
          return false
        }
        this.http.put(this.$store.state.prefix + '/activity', ai).then(res => {
          if (res.error === false) {
            this.$Notice.info({title: 'ä¿®æ”¹æˆåŠŸ', desc: 'æ´»åŠ¨æ·»åŠ æˆåŠŸ'})
            this.router.push('/activity');
          }
        })
        return true;
      },
      minusGroup (index) {
        this.Group.splice(index, 1)
      },
      addGroup (index) {
        if (this.Group[index].mans && this.Group[index].discount) {
          this.Group.push({mans: '', discount: ''})
        } else {
          this.$Notice.warning({title: 'è¯·å…ˆå®Œå–„å½“å‰é¡¹å†…å®¹'})
        }
      },
      getGiftList () {
        this.http.get(this.$store.state.prefix + '/gift/page/1').then(res => {
          if (res.error === false) {
            for (var i in res.result.records) {
              res.result.records[i].selected = false
            }
            this.GiftList = res.result.records
          }
        })
      },
      setGift (gift) {
        gift.selected = !gift.selected
      }
    }
  }
</script>

<style scoped lang='stylus' rel="stylesheet/stylus">
