<template>
  <div class="header">
    <div class="header-content">
      <Row>
        <Col span="8" class="syslogo">
        <img src="/static/images/logo.png" alt="">
        </Col>
        <Col span="16" class="dropmenus">
         <Menu mode="horizontal" :active-name="1" v-if="$store.state.token">
           <Submenu name="1">
            <template slot="title">
                <Icon type="stats-bars"></Icon>
                {{$store.state.companyName||userInfo.company.companyName}}
            </template>
            <Menu-group title="è´¦å·ç®¡ç†">
              <div style="margin:10px 0px;">
                <a href="javascript:;" @click="changePassword=true">ä¿®æ”¹å¯†ç </a>
              </div>
              <div>
                <a href="javascript:;" @click="loginOut()">æ³¨é”€</a>
              </div>
            </Menu-group>
        </Submenu>
        </Menu>
        </Col>
      </Row>
    </div>
    <Modal
      v-model="changePassword"
      title="ä¿®æ”¹å¯†ç "
      @on-ok="ok"
      @on-cancel="cancel">
      <Input type="password" v-model="oldPass" placeholder="è¯·è¾“å…¥æ—§å¯†ç " style="margin:10px 0;"></Input>
      <Input type="password" v-model="newPass" placeholder="è¯·è¾“å…¥æ–°å¯†ç "></Input>
    </Modal>

  </div>
</template>

<script type="text/ecmascript-6">
export default {
  name: 'Header',
  props: ['userInfo'],
  data () {
    return {
      changePassword:false,
      oldPass:'',
      newPass:''
    }
  },
  methods: {
    ok () {
      this.http.put(this.$store.state.prefix + '/company/updateAccountPassword',{
        oldPassword:this.oldPass,
        newPassword:this.newPass
      }).then((res)=>{
        if(res.error == false){
          this.$Notice.success({title: 'æé†’', desc: 'ä¿®æ”¹æˆåŠŸ'})
        }else{
          this.$Notice.error({title: 'é”™è¯¯', desc: res.msg})
        }
      });
      this.changePassword = false;
      this.oldPass = '';
      this.newPass = '';
    },
    cancel () {
      this.changePassword = false;
      this.oldPass = '';
      this.newPass = '';
    },
    loginOut () {
      this.$Notice.info({title: 'æé†’', desc: 'é€€å‡ºç™»å½•æˆåŠŸ'})
      this.$store.state.token=""
      this.util.delCookie("token")
    }
  },
  watch:{
    userInfo(val){
      this.userInfo=val
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='stylus' rel="stylesheet/stylus">
.header-content
  width: 1000px
  margin-left: auto
  margin-right: auto
  .syslogo
    text-align:left
    img
      height:70px
      width:auto
  .dropmenus
    .ivu-menu-horizontal
      height:67px
    .ivu-menu-submenu
      float:right
</style>



