<script  type='text/ecmascript-6'>
import uploader from '../Util/Uploader'
import ueditor from '../Ueditor'
export default {
  name: 'company',
  data () {
    return {
      title: 'å…¬å¸ä¿¡æ¯è®¾ç½®',
      type:1,
      parent:'basic',
      company: {
        id: '',
        companyName: '',
        companyDesc: '',
        companyAddr:'',
        companyTel: '',
        companyLogo: '',
        openSms:0,
        smsTel:'',
        toCashRate: 0,
        employeeRate: 0,
        sharePoints:0,
        shareMax: 0,
        registerMoney:0,
        registerPoints:0,
        selfReturn:0,
        oneReturn:0,
        secondReturn:0
      },
      Group: [
        {
          mans: '',
          discount: ''
        }
      ],
      areaOne: {
        id: null,
        name: ''
      },
      areaTwo: {
        id: null,
        name: ''
      },
      areaThree: {
        id: null,
        name: ''
      },
      areasOne: [],
      areasTwo: [],
      areasThree: [],
      uploaderconfig: {
        maxSize: 1024,
        format: ['jpg', 'png', 'jpeg'],
        showUploadList: false,
        parent: 'company',
        child: 'companyLogo'
      },
      defaultMSg: '',
      Ueditorconfig: {
        initialFrameWidth: null,
        initialFrameHeight: 320,
        info: {},
        imageUrl: '//up.qiniu.com/',
        imageActionName: 'uploadimage',
        imageFieldName: 'file',
        imageAllowFiles: ['.jpg', '.png', '.jpeg'],
        imageCompressEnable: true,
        imageCompressBorder: 1600,
        imageInsertAlign: 'none',
        imageUrlPrefix: '//oolds3geo.bkt.clouddn.com/',
        imagePathFormat: 'upload/image/{yyyy}{mm}{dd}/{time}{rand:6}'
      }
    }
  },
  components: { uploader ,ueditor},
  methods: {
    change (e, val) {
      if (val === 1) {
        this.areaOne.name = e.value;
        this.http.get(this.$store.state.prefix + '/company/getRegionInfo?parentId=' + e.value).then((res) => {
          if (res.error === false) {
            this.areasTwo = res.result;
          }
        });
      } else if (val === 2) {
        this.areaTwo.name = e.value;
        this.http.get(this.$store.state.prefix + '/company/getRegionInfo?parentId=' + e.value).then((res) => {
          if (res.error === false) {
            this.areasThree = res.result;
          }
        });
      } else {
        this.areaThree.name = e.value;
      }
    },
    minusGroup (index) {
      this.Group.splice(index, 1)
    },
    addGroup (index) {
      debugger
      if (this.Group[index].mans!=undefined && this.Group[index].mans!=="" && this.Group[index].discount!=="" && this.Group[index].discount!=undefined) {
        this.Group.push({mans: '', discount: ''})
      } else {
        this.$Notice.warning({title: 'è¯·å…ˆå®Œå–„å½“å‰é¡¹å†…å®¹'})
      }
    },
    editCompany () {
      var addr = '';
      if(this.areaThree.name != '' && this.areaThree.name != void 0){
        addr = this.areaThree.name;
      }
      this.company.companyAddrCode = this.areaOne.name + ',' + this.areaTwo.name + ',' + addr;
      console.log(this.company.companyAddrCode)
      if(!this._setGroupDiscount())return;
      if (!this._checkParamAndExecute()) return;
    },
    _checkParamAndExecute () {
      var ai = JSON.parse(JSON.stringify(this.company))
      if(this.type==1){
          if (!ai.companyDesc) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™ä¼ä¸šæè¿°'})
            return false
          }
          if (!ai.companyAddr) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™å…¬å¸åœ°å€'})
            return false
          }
          if (!ai.companyTel) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™è”ç³»æ–¹å¼'})
            return false
          }
          if (!ai.companyLogo) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·ä¸Šä¼ å…¬å¸logo'})
            return false
          }
      }else if(this.type==2){
        if (ai.toCashRate === '') {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™ç§¯åˆ†æŠµçŽ°é‡‘æ¯”ä¾‹'})
            return false
          }
          
          if (!ai.sharePoints) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™è½¬å‘ç§¯åˆ†'})
            return false
          }
          if (!ai.shareMax) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™æœ€å¤šè½¬å‘æ¬¡æ•°'})
            return false
          }
          if (!ai.registerMoney) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™æ³¨å†Œé‡‘é¢'})
            return false
          }
          if (this.util.isNull(ai.registerPoints)) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™æ³¨å†Œè¿”è¿˜ç§¯åˆ†'})
            return false
          }
          if (!ai.selfReturn) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™è‡ªå·±æ¶ˆè´¹è¿”è¿˜ç§¯åˆ†æ¯”çŽ‡'})
            return false
          }
          if (this.util.isNull(ai.oneReturn)) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™ä¸€çº§é‚€è¯·äººè¿”è¿˜ç§¯åˆ†æ¯”çŽ‡'})
            return false
          }
          if (!ai.secondReturn) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™äºŒçº§é‚€è¯·äººè¿”è¿˜ç§¯åˆ†æ¯”çŽ‡'})
            return false
          }
      }else if(this.type==3){
        if (!ai.smsTel) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™çŸ­ä¿¡æŽ¥æ”¶å·ç '})
            return false
          }
          if (!ai.employeeRate) {
            this.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™å‘˜å·¥ææˆæ¯”çŽ‡'})
            return false
          }
      }
      
      
      
      this.http.put(this.$store.state.prefix + '/company', this.company).then(res => {
        if (res.error === false) {
          this.$Message.success('ä¿®æ”¹æˆåŠŸ')
          this.router.push('/company?name='+this.parent)
        }
      })
      return true;
    },
    _setGroupDiscount(){
      var discountLevel = '[';
      var me = this;
      var isSuccess = true;
      this.Group.forEach(function (item, index) {
        if (index === 0) {
          if (me.util.isNull(item.mans) || me.util.isNull(item.discount)) {
            me.$Notice.info({title: 'è¯·å®Œå–„ä¿¡æ¯', desc: 'è¯·å¡«å†™å‘˜å·¥ææˆæ¯”ï¼Œæ¯”çŽ‡ä¸ºç™¾åˆ†æ¯”',duration:0})
            isSuccess = false
          }
        }
        if (item.discount > 100 || item.discount < 0) {
          me.$Notice.error({title: 'å‘˜å·¥ææˆæ¯”é”™è¯¯', desc: 'ç¬¬' + (parseInt(index) + 1) + 'é¡¹æŠ˜æ‰£ä¿¡æ¯éœ€åœ¨0-100ä¹‹é—´',duration:0})
          isSuccess = false
        }
        if (index > 0) {
          if (~~(item.mans) <= ~~(me.Group[index - 1].mans)) {
            me.$Notice.error({title: 'å‘˜å·¥ææˆæ¯”é”™è¯¯', desc: 'ç¬¬' + (parseInt(index) + 1) + 'é¡¹äººæ•°éœ€å¤§äºŽä¸Šä¸€é¡¹',duration:0})
            isSuccess = false
          }
          discountLevel += ','
        }
        discountLevel += '{"mans":'+item.mans + ',"discount":' + item.discount +'}'
      });
      this.company.employeeRate = discountLevel+']';
      if(isSuccess)
        return true;
      else
        return false;
    },
    Ueditorready (editor) {},
    Ueditorinput (obj) {
      this.company.show = obj.content
      this.Ueditorconfig.info = obj
    }
  },
  created () {
    var query = this.util.getQuery(location.hash);
    this.type=query.type
    this.parent=query.name
    if (this.$store.state.companyId) {
      this.http.get(this.$store.state.prefix + '/company/' + this.$store.state.companyId).then(res => {
